<template>
  <div class="class" @click.stop="">
    <div class="main">
      <div class="title">班级</div>
      <div class="add-class" @click="addClass">
        <img :src="addImg">
        <span>添加班级</span>
      </div>
      <div class="class-content">
        <div class="teaching-title">
          授课中的班级
        </div>
        <div class="teaching-content" v-if="teachingClassList.length>0">
          <div class="teaching-item" v-for="item in teachingClassList">
            <div class="class-name">{{item.name}}</div>
            <div class="class-post">{{item.post}}（{{item.teacher}}老师）</div>
            <div class="class-teacher-count">{{item.teacherCount}}人（老师）</div>
            <div class="class-student-count">{{item.studentCount}}人（学生）</div>
            <div class="operation" @click="closeClass(item)">
              <img :src="closeImg">
              <span>关闭班级</span>
            </div>
          </div>
        </div>
        <div v-else-if="teachingClassList.length == 0 && loading" class="no-teaching-content">
          <div class="item1">
            <img :src="addClassImg">
          </div>
          <div class="item2">
            快去添加班级，邀请其他老师和学生一起加入
          </div>
          <div class="item3" @click="addClass">
            <img :src="addImg">
            <span>添加班级</span>
          </div>
        </div>
        <div class="close-title">
          已关闭班级
        </div>
        <div class="teaching-content close-content">
          <div class="teaching-item close-item" v-for="item in closeClassList">
            <div class="class-name">{{item.name}}</div>
            <div class="class-post">{{item.name}}（{{item.teacher}}老师）</div>
            <div class="class-teacher-count">{{item.teacherCount}}人（老师）</div>
            <div class="class-student-count">{{item.studentCount}}人（学生）</div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      title="添加班级"
      :visible.sync="dialogVisible"
      class="add-class-content"
      width="392px">
      <div class="add-class">
        <div class="add-class-item">
          <div class="left">班级名称</div>
          <el-input v-model="name" placeholder="请输入内容"></el-input>
        </div>
        <div class="error" v-if="errorStatus">{{errorContent}}</div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="save">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="添加班级"
      :visible.sync="enoughVisible"
      class="enough-content"
      width="348px">
      <div class="enough-content-item">
        授课中的班级最多可添加10个，如需添加班级， 请关闭不需要的班级
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="enoughVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="关闭班级"
      :visible.sync="closeClassVisible"
      class="enough-content"
      width="348px">
      <div class="enough-content-item">
        确定要关闭{{closeClassName}}吗？点击确定后将不能再创建该班级的课程
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="closeClass_">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import moment from 'moment'
import closeImg from '@/assets/image/close.png'
import addImg from '@/assets/image/add.png'
import addClassImg from '@/assets/image/add-class.png'
import Api from '@/api'

export default {
  name: "Course",
  components: {
    
  },
  data() {
    return {
      closeImg,
      dialogVisible: false,
      // {
      //   name: '三年二班',
      //   post: '班主任',
      //   teacher: '张三',
      //   teacherCount: 3,
      //   studentCount: 30,
      //   id:'1',
      // }
      teachingClassList: [],
      closeClassList: [],
      name: '',
      errorStatus: false,
      errorContent: '课程名称不能为空',
      enoughVisible: false,
      closeClassVisible: false,
      closeClassName: '',
      closeClassId: '',
      addImg,
      addClassImg,
      loading: false
    };
  },
  mounted() {
    this.getClass()
  },
  methods: {
    getClass() {
      this.loading = false
      Api.Class.classList().then((val) => {
        console.log(val)
        let teachingClassList = []
        let closeClassList = []
        val.list.forEach((item) => {
          let obj = {
            post: '班主任',
            teacher: '张三',
            teacherCount: 3,
            studentCount: 30,
            name: item.name,
            id: item.id
          }
          if (item.closed) {
            closeClassList.push(obj)
          } else {
            teachingClassList.push(obj)
          }
        })
        this.teachingClassList = teachingClassList
        this.closeClassList = closeClassList
      }).finally(() => {
        this.loading = true
      })
    },
    addClass() {
      Api.Class.classList().then((val) => {
        if (val.count < 10) {
          this.name = ''
          this.errorContent = ''
          this.dialogVisible = true
        } else {
          this.enoughVisible = true
        }
      })
    },
    save() {
      if (!this.name) {
        this.errorStatus = true
        this.errorContent = '课程名称不能为空'
        return
      }
      Api.Class.addClass({name: this.name}).then(val => {
        console.log('val',val)
        let obj = {
          post: '班主任',
          teacher: '张三',
          teacherCount: 3,
          studentCount: 30,
          name: val.name,
          id: val.id
        }
        this.teachingClassList.push(obj)
        this.dialogVisible = false
      }).catch((err) => {
        this.errorStatus = true
        this.errorContent = err.data.err_msg
      })
    },
    closeClass(item) {
      this.closeClassName = item.name
      this.closeClassId = item.id
      this.closeClassVisible = true
    },
    closeClass_() {
      Api.Class.closeClass(this.closeClassId).then((vla) => {
        this.closeClassVisible = false
        this.getClass()
      })
    }
  },
  watch: {
    name(val) {
      if (val) {
        this.errorStatus = false
      }
    },
  }
}
</script>

<style scoped lang="less">
.class {
  width: calc(100% - 70px);
  margin-left: 70px;
  padding-top: 76px;
  height: calc(100% - 76px);
  .main {
    height: 100%;
    position: relative;
    .title {
      width:36px;
      height:18px;
      font-size:18px;
      font-family:AlibabaPuHuiTi-Medium,AlibabaPuHuiTi;
      font-weight:600;
      color:rgba(50,50,50,1);
      line-height:18px;
      margin-left: 28px;
    }
    .add-class {
      position: absolute;
      right: 34px;
      top: 10px;;
      width:118px;
      height:34px;
      // background:rgba(94,149,244,1);
      // border-radius:26px;
      line-height: 34px;
      font-size:14px;
      font-family:AlibabaPuHuiTi-Bold,AlibabaPuHuiTi;
      font-weight:bold;
      color:rgba(255,255,255,1);
      cursor: pointer;
      text-align: center;
      img {
        width: 100%;
        position: absolute;
        left: 0;
        top: -7px;
      }
      span {
        position: absolute;
        left: 42px;
        top: 2px;
      }
    }
    .class-content {
      background: rgba(245,247,248,1);
      position: absolute;
      margin-top: 48px;
      width: 100%;
      height: calc(100% - 66px);
      .teaching-title {
        font-size:12px;
        font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
        font-weight:400;
        color:rgba(167,167,167,1);
        margin-top: 30px;
        margin-left: 29px;
        margin-bottom: 12px;
      }
      .teaching-content {
        .teaching-item {
          margin-left: 39px;
          width: calc(100% - 60px);
          background:rgba(255,255,255,1);
          border-radius:6px;
          border:1px solid rgba(239,239,239,1);
          position: relative;
          height:80px;

          font-size:15px;
          font-family:AlibabaPuHuiTi-Medium,AlibabaPuHuiTi;
          font-weight:500;
          color:rgba(50,50,50,1);
          .class-name {
            position: absolute;
            left: 30px;
            top: 30px;
          }
          .class-post {
            position: absolute;
            top: 30px;
            left: 18%;
          }
          .class-teacher-count {
            position: absolute;
            top: 30px;
            left: 42%;
          }
          .class-student-count {
            position: absolute;
            top: 30px;
            left: 56%;
          }
          .operation {
            position: absolute;
            right: 30px;
            top: 25px;
            width:90px;
            height:30px;
            border-radius:4px 5px 5px 5px;
            border:1px solid rgba(94,149,244,1);
            font-size: 14px;
            line-height: 30px;
            text-align: center;
            font-weight:400;
            color:rgba(94,149,244,1);
            cursor: pointer;
            display: flex;
            justify-content: center;
            img {
              width: 16px;
              height: 16px;
              margin-top: 7px;
              margin-right: 2px;
            }
          }
        }
      }
      .no-teaching-content {
        background: #f5f7f8;
        width: 100%;
        height: 280px;
        text-align: center;
        margin-top: 23px;
        .item1 {
          img {
            width: 115px;
          }
        }
        .item2 {
          height: 20px;
          font-size: 14px;
          font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
          font-weight: 400;
          color: #85858E;
          line-height: 20px;
        }
        .item3 {
          position: relative;
       
          top: 10px;;
          width:108px;
          height:34px;
          // background:rgba(94,149,244,1);
          // border-radius:26px;
          line-height: 34px;
          font-size:14px;
          font-family:AlibabaPuHuiTi-Bold,AlibabaPuHuiTi;
          font-weight:bold;
          color:rgba(255,255,255,1);
          cursor: pointer;
          text-align: center;
          margin: auto;
          img {
            width: 100%;
            position: absolute;
            left: 0;
            top: -7px;
          }
          span {
            position: absolute;
            left: 37px;
          }
        }
      }
      
      .close-title {
        font-size:12px;
        font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
        font-weight:400;
        color:rgba(167,167,167,1);
        margin-top: 30px;
        margin-left: 29px;
        margin-bottom: 12px;
      }
      .close-item {
        background:rgba(245,247,248,1);
        border-radius:6px;
        border:1px solid rgba(239,239,239,1);
        color:rgba(167,167,167,1);
      }
    }
    
    /deep/.el-pagination {
      position: absolute;
      top: calc(100% - 90px);
      right: 23px;
      button {
        background: #FFFFFF;
      }
      .el-pager {
        li {
          background: #FFFFFF;
          &.active {
            background-color: #409EFF;
            color: #FFF
          }
        }
      }
    }
  }
  .add-class-content {
    /deep/.el-dialog__body {
      text-align: center;
    }
    /deep/.el-dialog__header {
      margin-bottom: 41px;
      .el-dialog__title {
        position: absolute;
        top: 29px;
        font-size:16px;
        font-family:AlibabaPuHuiTi-Medium,AlibabaPuHuiTi;
        font-weight:500;
        color:rgba(50,50,50,1);
      }
      .el-dialog__headerbtn {
        font-size: 29px;
        i {
          color: #989898
        }
        &:hover {
          i {
            color: #989898;
          }
        }
      }
    }
    .add-class-item {
      display: flex;
      line-height: 30px;
      .left {
        font-size:14px;
        font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
        font-weight:400;
        color:rgba(50,50,50,1);
      }
      .el-input {
        flex: 1;
        /deep/.el-input__inner {
          width: 260px;
          height: 30px;
          line-height: 30px;
        }
      }
    } 
    .dialog-footer {
      button {
        width:80px;
        height:30px;
        background:rgba(94,149,244,1);
        border-radius:4px;
        font-size:14px;
        font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
        font-weight:400;
        color:rgba(255,255,255,1);
        line-height: 30px;
        padding: 0;
      }
    }
    .error {
      font-size:10px;
      font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
      font-weight:400;
      color:rgba(245,96,96,1);
      position: absolute;
      left: 94px;
    }
  }
  .enough-content {
    /deep/.el-dialog__header {
      margin-bottom: 41px;
      .el-dialog__title {
        position: absolute;
        top: 29px;
        font-size:16px;
        font-family:AlibabaPuHuiTi-Medium,AlibabaPuHuiTi;
        font-weight:500;
        color:rgba(50,50,50,1);
      }
      .el-dialog__headerbtn {
        font-size: 29px;
        i {
          color: #989898
        }
        &:hover {
          i {
            color: #989898;
          }
        }
      }
    }
    .enough-content-item {
      font-size:14px;
      font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
      font-weight:400;
      color:rgba(50,50,50,1);
    }
  }
}

</style>



