<template>
  <div class="Login" v-if="loadingStatus">
    <div class="screen-bar" >
      <img :src="closeImg" class="close" @click="close">
    </div>
    <div v-if="status" class="login-content">
      <img :src="logoImg" class="logo">
      <el-input v-model="phone" placeholder="请输入手机号" width="300px"></el-input>
      <div class="save" @click="save">登录</div>
    </div>
  </div>
</template>

<script>
import closeImg from "@/assets/image/close2.png"
import logoImg from "@/assets/image/logo.png"
import Api from '@/api'
let ipc = null
if (window.require) {
  ipc = window.require('electron').ipcRenderer
} else {
  ipc = {
    send: function() {}
  }
}

export default {
  name: "Login",
  data() {
    return {
      phone: '',
      status: false,
      loading: null,
      closeImg,
      logoImg,
      loadingStatus: false
     
    };
  },
  mounted() {
    // this.openFullScreenLoading()
    if (localStorage.getItem('access_token')) {
      Api.User.userInfo().then((val) => {
        ipc.send('window-home')
        setTimeout(() => {
          // this.loading.close()
          this.loadingStatus = true
          this.$router.replace({ path: "/home"})
        }, 0);
      }).catch((err) => {
        ipc.send('window-login')
        setTimeout(() => {
          // this.loading.close()
          this.loadingStatus = true
          this.status = true
        }, 0);
      })
    } else {
      setTimeout(() => {
        ipc.send('window-login')
        this.status = true
        // this.loading.close()
        this.loadingStatus = true
        localStorage.setItem('access_token','')
      }, 0);
    }
  },
  methods: {
    save() {
      let params = {
        "grant_type":"mobile",
        "scope": ["user"],
        "mobile": this.phone,
        "sms_code": "11111",
        "sign": "iM94FwXz6tBVNjGAz47yqieistznShBR-iM94FwXz6tBVNjGAz47yqieistznShBR-1597473626859"
      }
      Api.Login.login(params).then((val) => {
        localStorage.setItem('access_token',val.access_token)
        ipc.send('window-home')
        setTimeout(() => {
          this.$router.replace({ path: "/home"})
        }, 0);
      })
    },
    openFullScreenLoading() {
      const loading = this.$loading({
        lock: true,
        text: 'Loading',
        // spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
      });
      this.loading = loading
    },
    close() { 
      ipc.send('window-close')
    },
    fangda() {
      ipc.send('window-max')
    },
    suoxiao() {
      ipc.send('window-min')
    }
  }
}
</script>

<style scoped lang="less">
.Login {
  background: #5E95F4;
  // border-radius: 12px;
  height: 100%;
  .screen-bar {
    height: 70px;
    position: fixed;
    width: 100%;
    // border-bottom: 1px solid #EFEFEF;
    left: 0px;
    -webkit-app-region: drag;
    .close {
      position: absolute;
      right: 21px;
      top: 20px;
      width: 17px;
      cursor: pointer;
      -webkit-app-region: no-drag;
    }
  }
  .login-content {
    width: 200px;
    margin: auto;
    padding-top: 117px;
    text-align: center;
    .logo {
      width: 85px;
      margin-bottom: 20px;
    }
    .save {
      width: 100%;
      height: 40px;
      background: white;
      border-radius: 4px;
      font-size: 14px;
      font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
      font-weight: 400;
      color: #ffffff;
      line-height: 40px;
      text-align: center;
      margin: auto;
      margin-top: 40px;
      cursor: pointer;
      color: #5E95F4;
    }
  }
}
</style>



