<template>
  <div class="chat">
    <div class="main">
      <div class="tab">
        <div class="left" :class="{select:type==0}" @click="chageType(0)">讨论</div>
        <div class="right" :class="{select:type==1}" @click="chageType(1)">成员({{number.length}})</div>
      </div>
      <div class="chat-content" >
        <div class="chat-box" v-show="type == 0" ref="chatBox">
          <div class="chat-box-item" v-for="item in messages" :key="item.messageId" :style="{'flex-direction':item.userRole == 2?'row-reverse':''}">
            <div class="item-common item-left" v-if="(item.userRole == 0 || item.userRole == 1)&&!item.isSelf">
              <div class="avatar" :style="{'background-image':`url(${item.avatar})`}"></div>
              <div class="message-content">
                <div class="item1" v-if="item.userRole == 0"><span class="span1">[学生]</span><span class="span2">{{item.nickname}}</span></div>
                <div class="item1" v-if="item.userRole == 1"><span class="span1">[老师]</span><span class="span2">{{item.nickname}}</span></div>
                <div class="item2">{{item.message}}</div>
              </div>
            </div>
            <div class="item-common item-right" v-else>
              <div class="avatar" :style="{'background-image':`url(${item.avatar})`}"></div>
              <div class="message-content">
                <div class="item1"><span class="span1">[老师]</span><span class="span2">{{item.nickname}}</span></div>
                <div class="item2">{{item.message}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="input-box-title">...... 老师发起全员禁言 ......</div>
        <div class="input-box" >
          <div class="input-box-content">
            <div class="item1">
              <img :src="allowImg" >
              <span>全体禁言</span> 
            </div>
            <el-input
              type="textarea"
              placeholder="请输入内容"
              resize="none"
              :rows="2"
              @keyup.enter.prevent.native='sendMessage'
              v-model="messageContent">
            </el-input>
            
          </div>
          <div class="send-message" @click="sendMessage">
            发送
          </div>
        </div>
        <div class="user-box" v-show="type == 1">
          <div class="chat-box-item" v-for="item in number" :key="item.userId">
            <div class="item-left">
              <div class="avatar" :style="{'background-image':`url(${item.avatar})`}"></div>
              <div class="name">{{item.nickname}}</div>
              <div class="role" v-if="item.userRole == 1">（老师）</div>
            </div>
            <div class="item-right">
              <span class="span1" v-if="item.forbidden">正常</span>
              <span class="span2" v-else>禁止</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import stopImg from "@/assets/image/live_btn_allowspeaking_press@2x.png"
import allowImg from "@/assets/image/live_btn_allowspeaking_press@2x.png"
import Api from "@/api"

export default {
  name: "Chat",
  props:['client','userId','chatId'],
  components: {
    
  },
  data() {
    return {
      type: 0,
      number:[
        // {userRole: 1,avatar: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3078898191,2318315632&fm=26&gp=0.jpg',nickname:'力胜',userId: '4',forbidden: false},
      ],
      messages: [
        // {userRole: 1,avatar: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3078898191,2318315632&fm=26&gp=0.jpg',message: '老师好，老师好，老师好，老师好老师好老师好老师好老师好，老师好',nickname:'王冲',userId: '4',messageId: 5,isSelf: true},
        // {userRole: 1,avatar: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3078898191,2318315632&fm=26&gp=0.jpg',message: '老师好，老师好，老师好，老师好老师好老师好老师好老师好，老师好',nickname:'王冲',userId: '4',messageId: 5,isSelf: false},
      ],
      allowImg,
      stopImg,
      messageContent: ''
    };
  },
  mounted() {
  },
  methods: {
    chageType(type) {
      this.type = type
    },
    onMessage() {
      this.client.on("message", (messages) => {
        console.info("user[%d] recv message :", this.userId, messages);
        let msgIds = [];
        for (let i = 0; i < messages.length; i++) {
          const msg = messages[i];
          msgIds.push(messages[i].id);
        }
        let msgs = this.client.getMessageById(msgIds);
        console.log('onmessage',msgs)
        for (let i = 0; i < msgs.length; i++) {
          //
          // this.appendMessage(msgs[i].user_id, msgs[i].content);
          if (msgs[i].type == 0) {
            let obj = this.getUserObj(msgs[i].user_id)
            obj.message = msgs[i].content
            obj.messageId = msgs[i].id
            this.messages.push(obj)
          }
        }
        this.$nextTick(() => {
          let boxElement = this.$refs.chatBox
          boxElement.scrollTop = boxElement.scrollHeight - boxElement.clientHeight
          boxElement = null
        })
      });
    },
    onSend() {
      this.client.on("send", (result) => {
        console.info("user[%d] send :", this.userId, result);
        let message = result.messages[0].content
        let obj = this.getUserObj(this.userId)
        obj = JSON.parse(JSON.stringify(obj))
        obj.messageId = result.messages[0].id
        obj.isSelf = true
        obj.message = message
        this.messages.push(obj)
        this.messageContent = ''
        this.$nextTick(() => {
          let boxElement = this.$refs.chatBox
          boxElement.scrollTop = boxElement.scrollHeight - boxElement.clientHeight
          boxElement = null
        })
      });
    },
    sendMessage() {
      let messageContent = this.messageContent
      messageContent = messageContent.replace(/(^\s*)|(\s*$)/g, "")
      if (!messageContent) {
        this.messageContent = ""
        return
      }
      if (this.client) {
        let message = messageContent
        let chatId = this.chatId
        chatId = parseInt(chatId) 
        let msgId = this.client.sendMessage(chatId, {
          type: 0,
          message: message,
          extension: JSON.stringify({}),
        })
        // let msgs = this.client.getMessageById([msgId])
        // console.info("sendMessage :", msgId)
        let obj = this.getUserObj(this.userId)
        obj = JSON.parse(JSON.stringify(obj))
        obj.messageId = msgId
        obj.isSelf = true
        obj.message = message
        this.messages.push(obj)
        this.messageContent = ''
        this.$nextTick(() => {
          let boxElement = this.$refs.chatBox
          boxElement.scrollTop = boxElement.scrollHeight - boxElement.clientHeight
          boxElement = null
        })
      }
    },
    getUsersByChatId(id) {
      Api.User.getUsersByChatId(id).then((val) => {
        console.log('val',val)
        let number = []
        val.list.forEach((value) => {
          let obj = {
            userRole: value.role_id,
            avatar: value.avatar,
            nickname: value.nickname,
            userId: value.id,
            forbidden: false
          }
          number.push(obj)
        })
        this.number = number
        console.log('this.number',number)
      })
    },
    getUserObj(userid) {
      let obj = {}
      for(let i = 0; i < this.number.length; i++) {
        if (this.number[i].userId == userid) {
          obj = this.number[i]
          break
        }
      }
      return obj
    }
  },
  watch: {
    'client': {
      handler(val) {
        console.log('client', val)
        if (val) {
          this.onMessage()
          // this.onSend()
        }
      },
      deep: true
    },
    'chatId': {
      handler(val) {
        if (val) {
          this.getUsersByChatId(val)
        }
      },
      deep: true
    },
  }
}
</script>

<style scoped lang="less">
  .chat {
    height: 100%;
    width: 272px;
    margin: auto;
    .main {
      height: calc(100% - 15px);
      .tab {
        height:30px;
        background:rgba(75,75,94,1);
        border-radius:6px 6px 0px 0px;
        display: flex;
        font-size:14px;
        font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
        font-weight:400;
        color:rgba(167,167,167,1);
        .left {
          width: 28px;
          margin-left: 56px;
          margin-right: 81px;
          line-height: 2;
          cursor: pointer;
        }
        .right {
          line-height: 2;
          cursor: pointer;
        }
        .select {
          font-weight:bold;
          color:rgba(94,149,244,1);
          border-bottom: 2px solid rgba(94,149,244,1);
        }
      }
      .chat-content {
        height: calc(100% - 30px);
        width: 100%;
        background: #585867;
        border-radius: 0px 0px 6px 6px;
        .chat-box {
          height: calc(100% - 187px);
          overflow-y: scroll;
          .chat-box-item {
            margin-top: 24px;
            .item-common {
              display: flex;
              margin-left: 10px;
              .avatar {
                width: 30px;
                height: 30px;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
                border-radius: 50%;
              }
              .message-content {
                margin-left: 6px;
                .item1 {
                  height:11px;
                  font-size:11px;
                  font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
                  font-weight:400;
                  line-height:11px;
                  .span1 {
                    color:#A0BBEC;
                    margin-right: 5px;
                  }
                  .span2 {
                    color: #92929B;
                  }
                }
                .item2 {
                  margin-top: 6px;
                  padding: 11px;
                  background:rgba(255,255,255,1);
                  border-radius:0px 6px 6px 6px;
                  font-size:13px;
                  font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
                  font-weight:400;
                  color:rgba(50,50,50,1);
                  max-width: 170px;
                }
              }
            }
            .item-right {
              flex-direction: row-reverse;
              margin-right: 10px;
              .message-content {
                margin-right: 6px;
                display: flex;
                flex-direction: column;
                .item1 {
                  align-self: flex-end;
                  .span1 {
                    color: #FFDF6C;
                  }
                }
                .item2 {
                  background: #5E95F4;
                  color: white;
                  border-radius:6px 0px 6px 6px;
                }
              }
            }
          }
        }
        ::-webkit-scrollbar {
          display: none; /* Chrome Safari */
        }
        .input-box-title {
          font-size:12px;
          font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
          font-weight:400;
          color:rgba(167,167,167,1);
          text-align: center;
          margin-top: 6px;
          margin-bottom: 5px;
          height: 12px;
          line-height: 12px;
          position: absolute;
          top: calc(100% - 200px);
          left: 50%;
          transform: translate(-50%, 0);
        }
        .input-box {
          height: 160px;
          background:rgba(109,109,125,1);
          border-radius:0px 0px 6px 6px;
          width: 272px;
          position: absolute;
          bottom: 16px;
          .input-box-content {
            padding-top: 5px;
            cursor: pointer;
            .item1 {
              width:80px;
              height:30px;
              border-radius:15px;
              border:1px solid rgba(255,255,255,1);
              font-size:12px;
              font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
              font-weight:400;
              color:rgba(255,255,255,1);
              display: flex;
              line-height: 30px;
              margin-left: 11px;
              cursor: pointer;
              img {
                width: 18px;
                margin-top: 5px;
                height: 18px;
                margin-left: 5px;
                margin-right: 2px;
              }
            }
            /deep/.el-textarea__inner {
              font-size:13px;
              font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
              font-weight:400;
              color:rgba(255,255,255,1);
              border: 0px;
              background: none;
              margin-top: 10px;
            }
          }
          .send-message {
            width:60px;
            height:30px;
            background:rgba(94,149,244,1);
            border-radius:4px;
            text-align: center;
            line-height: 30px;
            font-size:14px;
            font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
            font-weight:400;
            color:rgba(255,255,255,1);
            position: absolute;
            right: 10px;
            bottom: 10px;
            cursor: pointer;
          }
        }
        .user-box {
          height: calc(100% - 230px);
          overflow-y: scroll;
          .chat-box-item {
            display: flex;
            justify-content: space-between;
            margin-top: 10px;
            padding-left: 10px;
            padding-right: 10px;
            &:nth-child(1) {
              margin-top: 20px;
            }
            .item-left {
              display: flex;
              height: 30px;
              line-height: 30px;
              .avatar {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                background-repeat: no-repeat;
                background-position: center center;
                background-size: cover;
                margin-right: 5px;
              }
              .name, .role {
                font-size:14px;
                font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
                font-weight:400;
                color:rgba(239,239,239,1);
              }
            }
            .item-right {
              cursor: pointer;
              font-size:14px;
              font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
              font-weight:400;
              color:rgba(167,167,167,1);
            }
          }
        }
      }
    }
  }
  
</style>




