  mounted() {
    this.getTime()
    this.getCourse(0)
    setInterval(() => {
      this.getTime()
    }, 5000)
  },
  methods: {
    getCourse(type, refresh) {
      if (type == undefined) {
        type = this.type
      } else {
        this.type = type
      }
      console.log('type',type,this.type)
      this.allCoursewares = []
      if (type == 0) {
        // let currentCourse = this.currentCourse
        let params = {
          page: this.currentCoursePage,
          size: this.size
        }
        this.loading = false
        Api.Course.courseList({params: params}).then((val) => {
          console.log('val',val)
          let currentCourse = []
          this.total = val.count
          this.loading = true
          if (refresh == 'refresh') {
            this.$message({
              message: '刷新成功！',
              type: 'success',
              duration: 3000
            })
          }
          val.list.forEach((item) => {
            let obj = {}
            obj.id = item.id
            obj.startTime = item.begin_time
            obj.endTime = item.end_time
            obj.name = item.name
            obj.classes = item.classes
            obj.teachingMethod = item.type
            obj.coursewares = item.course_wares? (item.course_wares.length == 0 ? false:item.course_wares ): false,
            obj.isCancel = item.cancelled
            obj.finished = item.finished
            obj.chatId = item.chat_id
            currentCourse.push(obj)
            if (item.course_wares[0] && item.course_wares[0].images) {
              item.course_wares[0].images.forEach((img) => {
                this.allCoursewares.push(img)
              })
            }
          })
          this.currentCourse = currentCourse
          this.dataHandle(currentCourse)
          Lib.preloadimg(this.allCoursewares, 3)
          console.log('coursewares',this.currentCourse)
        }).catch((err) => {
          if (refresh == 'refresh') {
            this.$message({
              message: '刷新失败！',
              type: 'error'
            });
          }
        })
      } else if (type == 1) {
        let params = {
          page: this.historyCoursePage,
          size: this.size
        }
        this.loading = false
        Api.Course.historyCourseList({params: params}).then((val) => {
          console.log('val',val)
          let historyCourse = []
          this.total = val.count
          this.loading = true
          if (refresh == 'refresh') {
            this.$message({
              message: '刷新成功！',
              type: 'success'
            })
          }
          val.list.forEach((item) => {
            let obj = {}
            obj.id = item.id
            obj.startTime = item.begin_time
            obj.endTime = item.end_time
            obj.name = item.name
            obj.classes = item.classes
            obj.teachingMethod = item.type
            obj.coursewares = item.course_wares? (item.course_wares.length == 0 ? false:item.course_wares ): false,
            obj.isCancel = item.cancelled
            obj.finished = item.finished
            obj.chatId = item.chat_id
            historyCourse.push(obj)
            if (item.course_wares[0] && item.course_wares[0].images) {
              item.course_wares[0].images.forEach((img) => {
                this.allCoursewares.push(img)
              })
            }
          })
          this.historyCourse = historyCourse
          this.dataHandle(historyCourse)
          Lib.preloadimg(this.allCoursewares, 3)
          console.log('coursewares',this.historyCourse)
        }).catch((err) => {
          if (refresh == 'refresh') {
            this.$message({
              message: '刷新失败！',
              type: 'error'
            });
          }
        })
      }
    },
    dataHandle(course) {
      if (course.length == 0) {
        if (this.type == 0) {
          this.courseInfo = null
        } else if (this.type == 1){
          this.historyCourseInfo = null
        }
        return
      }
      let data = []
      //今天
      let current = moment().startOf('day').valueOf();
      let endtime = moment().endOf('day').valueOf();
      //明天
      let tomorrow = endtime + 1;
      let tomorrowEnd = tomorrow + 24 * 60 * 60 * 1000 - 1
      //后天
      let afterTomorrow = tomorrowEnd + 1
      let  afterTomorrowEnd = afterTomorrow + 24 * 60 * 60 * 1000- 1
      //昨天
      let yesterdayEnd = current - 1
      let yesterday = yesterdayEnd - 24 * 60 * 60 * 1000 + 1

      let currentYear = moment().year()
      let obj = {}
      course.forEach((item) => {
        let classes = ''
        for (let i = 0; i < item.classes.length; i++) {
          classes += item.classes[i].name
          if (i < item.classes.length - 1) {
            classes += ' | '
          }
        }
        classes = classes.trim()
        if (item.startTime >= current && item.startTime <= endtime) {
          if (!obj[current]) {
            obj[current] = {
              date : '今天（' + this.getWeek(item.startTime) + '）',
              data: [{
                time: moment(item.startTime).format('HH:mm') + '-' + moment(item.endTime).format('HH:mm'),
                duration: parseInt((item.endTime - item.startTime)/(1000 * 60)) + '（分钟）',
                id: item.id,
                startTime: item.startTime,
                endTime: item.endTime,
                name: item.name,
                classes: classes,
                coursewares: item.coursewares,
                isCancel: item.isCancel,
                finished:item.finished,
                teachingMethod: item.teachingMethod,
                classes_: item.classes,
                chatId: item.chatId
              }]
            }
          } else {
            obj[current].data.push({
              time: moment(item.startTime).format('HH:mm') + '-' + moment(item.endTime).format('HH:mm'),
              duration: parseInt((item.endTime - item.startTime)/(1000 * 60)) + '（分钟）',
              id: item.id,
              startTime: item.startTime,
              endTime: item.endTime,
              name: item.name,
              classes: classes,
              coursewares: item.coursewares,
              isCancel: item.isCancel,
              finished:item.finished,
              teachingMethod: item.teachingMethod,
              classes_: item.classes,
              chatId: item.chatId
            })
          }
        } else if (item.startTime >= tomorrow && item.startTime <= tomorrowEnd) {
          if (!obj[tomorrow]) {
            obj[tomorrow] = {
              date : '明天（' + this.getWeek(item.startTime) + '）',
              data: [{
                time: moment(item.startTime).format('HH:mm') + '-' + moment(item.endTime).format('HH:mm'),
                duration: parseInt((item.endTime - item.startTime)/(1000 * 60)) + '（分钟）',
                id: item.id,
                startTime: item.startTime,
                endTime: item.endTime,
                name: item.name,
                classes: classes,
                coursewares: item.coursewares,
                isCancel: item.isCancel,
                finished:item.finished,
                teachingMethod: item.teachingMethod,
                classes_: item.classes,
                chatId: item.chatId
              }]
            }
          } else {
            obj[tomorrow].data.push({
              time: moment(item.startTime).format('HH:mm') + '-' + moment(item.endTime).format('HH:mm'),
              duration: parseInt((item.endTime - item.startTime)/(1000 * 60)) + '（分钟）',
              id: item.id,
              startTime: item.startTime,
              endTime: item.endTime,
              name: item.name,
              classes: classes,
              coursewares: item.coursewares,
              isCancel: item.isCancel,
              finished:item.finished,
              teachingMethod: item.teachingMethod,
              classes_: item.classes,
              chatId: item.chatId
            })
          }
        } else if (item.startTime >= afterTomorrow && item.startTime <= afterTomorrowEnd) {
          if (!obj[afterTomorrow]) {
            obj[afterTomorrow] = {
              date : '后天（' + this.getWeek(item.startTime) + '）',
              data: [{
                time: moment(item.startTime).format('HH:mm') + '-' + moment(item.endTime).format('HH:mm'),
                duration: parseInt((item.endTime - item.startTime)/(1000 * 60)) + '（分钟）',
                id: item.id,
                startTime: item.startTime,
                endTime: item.endTime,
                name: item.name,
                classes: classes,
                coursewares: item.coursewares,
                isCancel: item.isCancel,
                finished:item.finished,
                teachingMethod: item.teachingMethod,
                classes_: item.classes,
                chatId: item.chatId
              }]
            }
          } else {
            obj[afterTomorrow].data.push({
              time: moment(item.startTime).format('HH:mm') + '-' + moment(item.endTime).format('HH:mm'),
              duration: parseInt((item.endTime - item.startTime)/(1000 * 60)) + '（分钟）',
              id: item.id,
              startTime: item.startTime,
              endTime: item.endTime,
              name: item.name,
              classes: classes,
              coursewares: item.coursewares,
              isCancel: item.isCancel,
              finished:item.finished,
              teachingMethod: item.teachingMethod,
              classes_: item.classes,
              chatId: item.chatId
            })
          }
        } else if (item.startTime >= yesterday && item.startTime <= yesterdayEnd) {
          if (!obj[yesterday]) {
            obj[yesterday] = {
              date : '昨天（' + this.getWeek(item.startTime) + '）',
              data: [{
                time: moment(item.startTime).format('HH:mm') + '-' + moment(item.endTime).format('HH:mm'),
                duration: parseInt((item.endTime - item.startTime)/(1000 * 60)) + '（分钟）',
                id: item.id,
                startTime: item.startTime,
                endTime: item.endTime,
                name: item.name,
                classes: classes,
                coursewares: item.coursewares,
                isCancel: item.isCancel,
                finished:item.finished,
                teachingMethod: item.teachingMethod,
                classes_: item.classes,
                chatId: item.chatId
              }]
            }
          } else {
            obj[yesterday].data.push({
              time: moment(item.startTime).format('HH:mm') + '-' + moment(item.endTime).format('HH:mm'),
              duration: parseInt((item.endTime - item.startTime)/(1000 * 60)) + '（分钟）',
              id: item.id,
              startTime: item.startTime,
              endTime: item.endTime,
              name: item.name,
              classes: classes,
              coursewares: item.coursewares,
              isCancel: item.isCancel,
              finished:item.finished,
              teachingMethod: item.teachingMethod,
              classes_: item.classes,
              chatId: item.chatId
            })
          }
        } else {
          let day = moment(item.startTime).startOf('day').valueOf()
          let year = moment(item.startTime).year()
          if (!obj[day]) {
            obj[day] = {
              date : (year != currentYear?moment(item.startTime).format('YYYY-MM-DD') :moment(item.startTime).format('MM-DD')) + '（' + this.getWeek(item.startTime) + '）',
              data: [{
                time: moment(item.startTime).format('HH:mm') + '-' + moment(item.endTime).format('HH:mm'),
                duration: parseInt((item.endTime - item.startTime)/(1000 * 60)) + '（分钟）',
                id: item.id,
                startTime: item.startTime,
                endTime: item.endTime,
                name: item.name,
                classes: classes,
                coursewares: item.coursewares,
                isCancel: item.isCancel,
                finished:item.finished,
                teachingMethod: item.teachingMethod,
                classes_: item.classes,
                chatId: item.chatId
              }]
            }
          } else {
            obj[day].data.push({
              time: moment(item.startTime).format('HH:mm') + '-' + moment(item.endTime).format('HH:mm'),
              duration: parseInt((item.endTime - item.startTime)/(1000 * 60)) + '（分钟）',
              id: item.id,
              startTime: item.startTime,
              endTime: item.endTime,
              name: item.name,
              classes: classes,
              coursewares: item.coursewares,
              isCancel: item.isCancel,
              finished:item.finished,
              teachingMethod: item.teachingMethod,
              classes_: item.classes,
              chatId: item.chatId
            })
          }
        }
      })
      console.log('obj',obj)
      if (this.type == 0) {
        this.courseInfo = obj
      } else if (this.type == 1) {
        this.historyCourseInfo = obj
      }
    },
    getWeek(time) {
      let day = moment(time).day()
      let week = ''
      switch(day) {
        case 0:
          week = '周日'
          break
        case 1:
          week = '周一'
          break
        case 2:
          week = '周二'
          break
        case 3:
          week = '周三'
          break
        case 4:
          week = '周四'
          break
        case 5:
          week = '周五'
          break
        case 6:
          week = '周六'
          break
        default:
          break
      }
      return week
    },
    classEnter(e) {
      let nextElementSibling = e.target.nextElementSibling
      nextElementSibling.style.display ='block'
    },
    classLeave(e) {
      let nextElementSibling = e.target.nextElementSibling
      nextElementSibling.style.display ='none'
    },
    addCourse() {
      // this.dialogVisible = true
      Api.Class.classList().then((val) => {
        if (val.count == 0) {
          this.dialogVisible = true
        } else {
          this.$store.commit('courseModal/setCourseData',null)
          this.$store.commit('courseModal/setCourseModalStatus',true)
        }
      })
    },
    editCourse(course) {
      console.log('course',course)
      let classes = course.classes_.map((item) => {
        return item.id
      })
      let startTime = moment(course.startTime)
      let endTime = moment(course.endTime)
      let selectMinuteDuration = ((endTime - startTime) % (1000 * 3600)) / (60 * 1000)

      let data = {
        name: course.name,
        statTimeDate: startTime.format('YYYY-MM-DD'),
        selectHour: startTime.format('HH'),
        selectMinute: startTime.format('mm'),
        selectHourDuration: '0' + parseInt((endTime - startTime) / (1000 * 3600)),
        selectMinuteDuration: selectMinuteDuration <=5? '0'+selectMinuteDuration:selectMinuteDuration,
        classes: classes,
        id: course.id,
        teachingMethod: course.teachingMethod
      }
      this.$store.commit('courseModal/setCourseModalStatus', true)
      this.$store.commit('courseModal/setCourseData',data)
    },
    deleteCourseware() {
      if (this.type == 1) {
        return
      }
      let coursewareId = this.coursewares.id
      let that = this
      Api.Course.deleteCourseware(coursewareId).then((val) => {
        let course_id = that.coursewares.course_id
        let obj = {
          course_id: course_id,
          list_type: 'all'
        }
        this.$message({
          message: '删除课件成功！',
          type: 'success',
          duration: 3000
        })
        Api.Course.coursewareList(course_id,{params:obj}).then(val => {
          if (val.count == 0 ) {
            that.checkCourseVisible = false
            setTimeout(() => {
              that.coursewares = []
              that.coursewareIndex = 0
            }, 0);
            this.getCourse(0)
          } else {
            that.coursewares = val.list
          }
        }).catch((err) => {

        })
      })
      
    },
    checkCourse(course_wares) {
      this.checkCourseVisible = true
      this.coursewares = course_wares[0]
    },
    switchImg(type) {
      if (type == 'left') {
        if (this.coursewareIndex > 0) {
          this.coursewareIndex--
        }
      }
      if (type == 'right') {
        if (this.coursewareIndex < this.coursewares.images.length -1) {
          this.coursewareIndex++
        }
      }
    },
    currentChange(page) {
      if (this.type == 0) {
        this.currentCoursePage = page
      } else {
        this.historyCoursePage = page
      }
      let type = this.type
      this.getCourse(type)
    },
    uploadCourse(id) {
      this.uploadCourseVisible = true
      this.uploadCourseId = id
      this.progress = 0
    },
    uploadFile(e) {
      let limit = 2;
      // let maxSize = limit * 1024 * 1024; // 限制上传文件大小2M以内
      let formData = new FormData();
      let file = e.target.files[0];
      console.log('file.name',file.name)
      let filename = file.name
      let a = filename.split('').reverse().join('')
      let b = a.substring(0,a.search(/\./)).split('').reverse().join('')
      if (b != 'ppt' && b!='pptx' && b!='doc' && b!='docx') {
        this.$message.error('请上传PPT、PPTX、DOC、DOCX类型的文件');
        this.$refs.inputFile.value = null
        return
      }
      // if (file.size > maxSize) {
      //   this.$Message.error({
      //     background: true,
      //     duration: 3,
      //     content: `上传文件不能大于${limit}M`
      //   });
      //   return false;
      // }
      let course_id = this.uploadCourseId
      // formData.append('course_id',course_id)
      formData.append("filename", file.name);
      formData.append("file", file);
      this.fileName = file.name
      this.progress = 1
      Api.Course.uploadFile(course_id,formData)
        .then(req => {
          // let data = res.data.data;
          console.log('success',req)
          this.$refs.inputFile.value = null
          this.timer = setInterval(() => {
            Api.Course.checkUploadStatus(req.id).then(value => {
              this.progress = value.progress
              if (value.progress == 100) {
                clearInterval(this.timer)
                this.timer = null
                this.getCourse(0)
              }
            })
          }, 500);
          
        })
        .finally(() => {
          // this.loading = false;
        });
    },
    goUpload() {
      if (this.progress > 0) {
        return
      }
      this.$refs.inputFile.click()
    },
    cancelCourse(id) {
      Api.Course.cancelCourse(id).then((val) => {
        this.getCourse(0)
      }).catch((err) => {

      })
    },
    getTime() {
      this.localCurrentTime = moment().valueOf()
      console.log('localCurrentTime',this.localCurrentTime,)
    },
    enterRoom(item) {
      console.log('item',item)
      this.$router.replace({ path: "/room",query:{chatId: item.chatId,courseId: item.id,endTime: item.endTime}})
    }
  },
  watch: {
    uploadCourseVisible(val) {
      if (!val) {
        this.nameError = false
        this.$refs.inputFile.value = null
        this.fileName = ''
      }
    },
  }
}
</script>

