<template>
  <div class="screen-bar">
    <img :src="closeImg" class="close" @click="close">
    <img :src="fangdaImg" class="fangda" @click="fangda">
    <img :src="suoxiaoImg" class="suoxiao" @click="suoxiao">
  </div>
</template>

<script>
import closeImg from "@/assets/image/close1.png"
import fangdaImg from "@/assets/image/fangda.png"
import suoxiaoImg from "@/assets/image/suoxiao.png"
let ipc = null
if (window.require) {
  ipc = window.require('electron').ipcRenderer
}

export default {
  name: "leftBar",
  data() {
    return {
      closeImg,
      fangdaImg,
      suoxiaoImg
    };
  },
  mounted() {
    
  },
  methods: {
    close() { 
      // ipc.send('window-close')
      this.$emit('closeRoom')
    },
    fangda() {
      ipc.send('window-max')
    },
    suoxiao() {
      ipc.send('window-min')
    }
  }
};
</script>

<style scoped lang="less">
.screen-bar {
  height: 53px;
  position: fixed;
  width: 100%;
  // border-bottom: 1px solid #EFEFEF;
  // left: 70px;
  -webkit-app-region: drag;
  z-index: 1;
  .close {
    position: absolute;
    right: 21px;
    top: 20px;
    width: 17px;
    cursor: pointer;
    -webkit-app-region: no-drag;
  }
  .fangda {
    position: absolute;
    right: 48px;
    top: 22px;
    height: 14px;
    width: 18px;
    cursor: pointer;
     -webkit-app-region: no-drag;
  }
  .suoxiao {
    position: absolute;
    right: 75px;
    top: 15px;
    width: 30px;
    cursor: pointer;
     -webkit-app-region: no-drag;
  }
}
</style>



