export default {
  namespaced: true,
  state: {
    courseModalStatus:false,
    courseData: null
  },
  getters: {
  },
  mutations: {
    setCourseModalStatus(state, data) {
      console.log('data',data)
      state.courseModalStatus = data
    },
    setCourseData(state, data) {
      state.courseData = data
    }
  },
  actions: {
  }
};


