import Axios from 'axios';
// 备注：, 在参数内传递 {msgAlert: false}，可以在接口返回错误的时候不提示错误信息
export default {
  classList() {
    return Axios.get('/api/v1/classes')
  },
  addClass(params) {
    return Axios.post('/api/v1/classes', params)
  },
  closeClass(id) {
    return Axios.put(`/api/v1/classes/${id}/close`)
  },
};



