import Axios from 'axios';
// 备注：, 在参数内传递 {msgAlert: false}，可以在接口返回错误的时候不提示错误信息
export default {
  userInfo() {
    return Axios.get('/api/v1/user/info');
  },
  getUsersByChatId(chatId) {
    return Axios.get(`/api/v1/chats/${chatId}/users`);
  } 
};



