<template>
  <div class="screen-bar">
    <img :src="closeImg" class="close" @click="close">
    <img :src="fangdaImg" class="fangda" @click="fangda">
    <img :src="suoxiaoImg" class="suoxiao" @click="suoxiao">
    <div class="split"></div>
    <img :src="refreshImg" class="refresh" @click="refresh">
  </div>
</template>

<script>
import closeImg from "@/assets/image/close1.png"
import fangdaImg from "@/assets/image/fangda.png"
import suoxiaoImg from "@/assets/image/suoxiao.png"
import refreshImg from "@/assets/image/home_refresh.png"
let ipc = null
if (window.require) {
  ipc = window.require('electron').ipcRenderer
}

export default {
  name: "leftBar",
  data() {
    return {
      closeImg,
      fangdaImg,
      suoxiaoImg,
      refreshImg
    };
  },
  mounted() {
    
  },
  methods: {
    close() { 
      ipc.send('window-close')
    },
    fangda() {
      ipc.send('window-max')
    },
    suoxiao() {
      ipc.send('window-min')
    },
    refresh() {
      this.$emit('refresh')
    }
  }
};
</script>

<style scoped lang="less">
.screen-bar {
  height: 54px;
  position: fixed;
  width: calc(100% - 70px);
  border-bottom: 1px solid #EFEFEF;
  left: 70px;
  -webkit-app-region: drag;
  .close {
    position: absolute;
    right: 21px;
    top: 20px;
    width: 17px;
    cursor: pointer;
    -webkit-app-region: no-drag;
  }
  .fangda {
    position: absolute;
    right: 48px;
    top: 22px;
    height: 14px;
    width: 18px;
    cursor: pointer;
     -webkit-app-region: no-drag;
  }
  .suoxiao {
    position: absolute;
    right: 75px;
    top: 15px;
    width: 30px;
    cursor: pointer;
     -webkit-app-region: no-drag;
  }
  .split {
    height: 16px;
    width: 0px;
    border: 1px solid #EFEFEF;
    position: absolute;
    right: 114px;
    top: 21px;
  }
  .refresh {
    position: absolute;
    right: 130px;
    top: 19px;
    width: 22px;
    cursor: pointer;
     -webkit-app-region: no-drag;
  }
}
</style>



