import Axios from 'axios';
// 备注：, 在参数内传递 {msgAlert: false}，可以在接口返回错误的时候不提示错误信息
export default {
  courseList(params={}) {
    return Axios.get('/api/v1/courses',params)
  },
  addCourse(params) {
    return Axios.post('/api/v1/courses',params)
  },
  cancelCourse(id) {
    return Axios.put(`/api/v1/courses/${id}/cancel`)
  },
  historyCourseList(params={}) {
    return Axios.get('/api/v1/courses/history', params)
  },
  updateCourse(id, params) {
    return Axios.put(`/api/v1/courses/${id}`, params)
  },
  deleteCourseware(course_ware_id) {
    return Axios.delete(`/api/v1/course_wares/${course_ware_id}`)
  },
  coursewareList(courseId,params={}) {
    return Axios.get(`/api/v1/courses/${courseId}/course_wares`,params)
  },
  finishCourse(id) {
    return Axios.put(`/api/v1/courses/${id}/finish`)
  },
  uploadFile(courseId,params) {
    return Axios.post(`/api/v1/courses/${courseId}/course_wares`,params);
  },
  checkCourse(params) {
    return Axios.get('/api/v1/courses/check',params)
  },
  checkUploadStatus(course_ware_id) {
    return Axios.get(`/api/v1/course_wares/${course_ware_id}/transform`)
  },
};



