// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Axios from 'axios'
import vueCookie from 'vue-cookie'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import "./assets/css/main.less"
import store from './store';

Vue.use(ElementUI)
Vue.use(vueCookie)
Vue.config.productionTip = false

let api = 'http://boss-tob-dev.dding.net:7001'
Axios.interceptors.request.use(config => {
  let onLine = navigator.onLine
  
  // 统一配置请求地址
  config.baseURL = api;
  config.headers.client_id = 'iM94FwXz6tBVNjGAz47yqieistznShBR'
  if (localStorage.getItem('access_token')) {
    config.headers.access_token = localStorage.getItem('access_token')
  }
  // if (!config.headers.Authorization) {
  //   // 单个接口内没有配置token ，则统一配置
  //   let token = vueCookie.get('token_s') || ''
  //   config.headers.Authorization = `Bearer ${token}`
  // }
  return config
}, error => {
  return Promise.reject(error)
})

// 响应拦截器
Axios.interceptors.response.use(response => {
  return response.data.result
}, error => {
  return Promise.reject(error.response)
})

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



