<template>
  <div class="count-down" >
    <img :src="timeImg" :style="{right: !status?'312px':''}">
    <div class="count-down-item"  v-if="status">
      <span class="span1" :style="{right: '48px'}">倒计时：</span>
      <span class="span2">{{hour}}:</span>
      <span class="span3">{{minute}}:</span>
      <span class="span4">{{second}}</span>
    </div>
    <div class="count-down-item" v-else>
      <span class="span1">拖堂时间：</span>
      <span class="span2">{{hour}}:</span>
      <span class="span3">{{minute}}:</span>
      <span class="span4">{{second}}</span>
    </div>
    <el-dialog
      :show-close="false"
      :visible.sync="courseWillEndStatus"
      width="30%"
      class="course-will-end"
      >
      <span class="course-will-end-content">即将下课，请合理安排时间！</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="courseWillEndStatus = false">知道了</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="下课提示"
      :show-close="false"
      :visible.sync="tipStatus1"
      width="30%"
      class="course-tip1"
      >
      <div class="course-tip-item1">{{countDown}}</div>
      <div class="course-tip-item2">您已拖堂{{delayTime}}分钟,是否继续上了？如不选择教室即将关闭。</div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="finishCourse">下课</el-button>
        <el-button type="primary" @click="continutCourse">上课</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="下课提示"
      :show-close="false"
      :visible.sync="tipStatus2"
      width="30%"
      class="course-tip2"
      >
      <div class="course-tip-item">本节课已结束，由于您长时间未操作，系统已自动关闭教室。</div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="gotoHome">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import timeImg from "@/assets/image/time.png"
import Api from "@/api"

export default {
  name: "leftBar",
  props: ['endTime','courseId'],
  data() {
    return {
      timeImg,
      hour: '00',
      minute: '00',
      second: '00',
      inter: null,
      status: true,
      courseWillEndStatus: false,
      finishCourseWillEndStatus: false,
      inter2: null,
      tipStatus1: false,
      tipStatus2: false,
      finishTipStatus: false,
      countDown: 20,
      delayTime: 0
    };
  },
  mounted() {
    setTimeout(() => {
      this.init_()
    }, 0);
  },
  methods: {
    init_() {
      let date = new Date()
      if (this.endTime > date.getTime()) {
        this.init(date.getTime())
      } else {
        this.status = false
        this.init(date.getTime())
      }
      if (this.inter) {
        clearInterval(this.inter)
        this.inter = setInterval(() => {
          let date = new Date()
          if (this.endTime > date.getTime()) {
            this.status = true
            this.init(date.getTime())
          } else {
            this.status = false
            this.init(date.getTime())
          }
        }, 1000);
      } else {
        this.inter = setInterval(() => {
          let date = new Date()
          if (this.endTime > date.getTime()) {
            this.status = true
            this.init(date.getTime())
          } else {
            this.status = false
            this.init(date.getTime())
          }
        }, 1000);
      }
    },
    init(time) {
      let differ = (this.endTime - time) / 1000
      if (differ < 0 ) {
        differ = -differ
      }
      let hour = parseInt(differ / 3600)
      if (hour < 10) {
        hour = '0' + hour
      }
      // console.log('hour', hour)
      this.hour = hour
      let minute = differ % 3600
      minute = parseInt(minute / 60)
      if (minute < 10) {
        minute = '0' + minute
      }
      this.minute = minute
      let second = parseInt((differ % 3600) % 60)
      if (second < 10) {
        second = '0' + second
      }
      this.second = second
      //处理modal
      if (this.endTime - time < 5*60*1000 && this.endTime - time >= 5*60*1000-5000 && !finishCourseWillEndStatus) {
        this.courseWillEndStatus = true
        this.finishCourseWillEndStatus = true
      }
      if (time - this.endTime > 2000 && (time - this.endTime)%(5*60*1000) < 1000 && !this.tipStatus1 && !this.finishTipStatus) {
        this.courseWillEndStatus = false
        this.tipStatus1 = true
        this.countDown = 20
        let delayTime = (time - this.endTime)/(60*1000)
        delayTime = parseInt(delayTime)
        this.delayTime = delayTime
        if (this.inter2) {
          clearInterval(this.inter2)
        }
        this.inter2 = setInterval(() => {
          if (this.countDown > 0) {
            this.countDown--
          }
          if (this.countDown == 0) {
            //结束课程
            Api.Course.finishCourse(this.courseId).then(val => {
              this.tipStatus2 = true
              this.tipStatus1 = false
              this.finishTipStatus = true
              if (this.inter2) {
                clearInterval(this.inter2)
                this.inter2 = null
              }
              this.$emit('stopRoom')
            }) 
          }
        }, 1000)
      }
    },
    finishCourse() {
      Api.Course.finishCourse(this.courseId).then(val => {
        this.tipStatus1 = false
        if (this.inter2) {
          clearInterval(this.inter2)
          this.inter2 = null
        }
        this.$emit('closeRoom')
      })
    },
    continutCourse() {
      this.tipStatus1 = false
      if (this.inter2) {
        clearInterval(this.inter2)
        this.inter2 = null
      }
    },
    gotoHome() {
      this.tipStatus2 = false
      this.$router.replace({ path: "/home"})
    },
  },
  beforeDestroy() {
    if (this.inter) {
      clearInterval(this.inter)
      this.inter = null
    }
    if (this.inter2) {
      clearInterval(this.inter2)
      this.inter2 = null
    }
  },
  watch: {
    'tipStatus1': {
      handler(val) {
        if (!val) {
          if (this.inter2) {
            clearInterval(this.inter2)
            this.inter2 = null
          }
        }
      },
      deep: true
    },
  }
};
</script>

<style scoped lang="less">
.count-down {
  position: absolute;
  right: 0;
  img {
    width: 14px;
    height: 14px;
    position: absolute;
    right: 300px;
    top: 21px;
  }
  .count-down-item {
    font-size:12px;
    font-family:AlibabaPuHuiTi-Regular,AlibabaPuHuiTi;
    font-weight:400;
    color:rgba(255,255,255,1);
    margin-right: 187px;
    margin-top: 19px;
    position: relative;
    .span1 {
      position: absolute;    
      right: 59px;
      width: 60px;
    }
    .span2 {
      position: absolute;
      right: 45px;
    }
    .span3 {
      position: absolute;
      right: 28px;
    }
    .span4 {
      position: absolute;
      right: 12px;
    }
  }
  .course-will-end {
    .course-will-end-content {
      text-align: center;
      display: inherit;
      font-size: 14px;
      font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
      font-weight: 400;
      color: #323232;
    }
  }
  .course-tip1 {
    .course-tip-item1 {
      font-size: 22px;
      font-family: AlibabaPuHuiTi-Medium, AlibabaPuHuiTi;
      font-weight: 500;
      color: #5E95F4;
      line-height: 20px;
      text-align: center;
    }
    .course-tip-item2 {
      font-size: 14px;
      font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
      font-weight: 400;
      color: #323232;
      margin-top: 30px;
    }
  }
  .course-tip2 {
    .course-tip-item {
      font-size: 14px;
      font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
      font-weight: 400;
      color: #323232;
    }
  }
  /deep/.el-dialog {
    border-radius: 6px;
  }
}
</style>



