          </div>
          <div class="bottom-item" @click="startRaisehand">
            <img :src="raisehandStatus?raisehandSelectImg:raisehandImg" />
            <span>举手</span>
          </div>
        </div>
      </div>
      <div class="content-right">
        <div class="player">
          <canvas id="teacher-player" width="272" height="153" v-show="!shiftStatus && startCameraStatus"></canvas>
          <div class="teacher-name" v-show="!shiftStatus && startCameraStatus">{{teacherName}}老师</div>
          <div class="courseware" v-show="shiftStatus" :style="{background:!coursewares?'#585867':''}">
            <div v-if="!coursewares" class="no-course">
              <div class="no-course-content">
                <div class="yuan">
                  <img :src="noCourseImg" alt="">
                </div>
                <div class="bottom-item">暂无课件，如需上传请退出教室上传课件～</div>
              </div>
            </div>
            <div
              class="course-img"
              v-else-if="coursewares.images.length > 0"
              :class="{'student-on-stage':stageStudent.length>0}"
            >
              <img :src="coursewares.images[coursewareIndex]" class="courseware-img" />
              <div class="jiantou">
                <img
                  :src="coursewareIndex == 0?leftRowImg1:leftRowImg"
                  :style="{cursor:coursewareIndex == 0?'not-allowed':'pointer'}"
                  class="left-row"
                  @click="switchImg('left')"
                />
                <img
                  :src="coursewareIndex == coursewares.images.length -1?rightRowImg1:rightRowImg"
                  :style="{cursor:coursewareIndex == coursewares.images.length-1?'not-allowed':'pointer'}"
                  class="right-row"
                  @click="switchImg('right')"
                />
              </div>
            </div>
          </div>
          <div class="start-camera" v-show="!startCameraStatus && !shiftStatus">
            <div class="start-camera-item" @click="startCamera_">
              <span class="span1"><img :src="stopImg"></span>
              <span class="span2">开启视频</span>
            </div>
          </div>
          <div class="shift-img" @click="shiftPlayerScreen">
            <img :src="shiftImg" />
          </div>
          <div class="turnoffcamera-img" @click="closeCamera">
            <img :src="turnoffcameraImg" />
          </div>
        </div>
        <div class="content-chat">
          <Chat :client='client' :userId='userId' :chatId='chatId'/>
        </div>
      </div>
    </div>
    <div class="modal-sign" v-show="modalSignStatus" @click="vModal">
      <div class="modal-sign-content" @click.stop>
        <div class="modal-sign-header">签到</div>
        <img :src="suoxiaoImg" class="suoxiaoImg" @click="suoxiao" />
        <div class="modal-sign-main">
          <div class="modal-sign-main-item">课程结束后可在考勤中查看签到明细</div>
          <div class="modal-sign-main-item2">
            <div class="item">
              <div class="count no-sign-count">{{number.length - signNumber.length}}</div>
              <div class="content">未签到人数</div>
            </div>
            <div class="item">
              <div class="count sign-count">{{signNumber.length}}</div>
              <div class="content">已签到人数</div>
            </div>
            <div class="item">
              <div class="count">{{number.length}}</div>
              <div class="content">班级总人数</div>
            </div>
          </div>
        </div>
        <div class="finish-sign" @click="finishSign">结束签到</div>
      </div>
    </div>
    <div class="modal-raisehand" v-show="modalRaisehandStatus" @click="vModal">
      <div class="modal-content" @click.stop>
        <div class="modal-header">举手</div>
        <img :src="suoxiaoImg" class="suoxiaoImg" @click="suoxiao" />
        <div class="modal-main">
          <div class="modal-title">
            <span>举手人数：</span>
            <span class="raisehand-count">{{raisehandStudent.length}}</span>
            <span class="all-count">/{{number.length}}</span>
          </div>
          <div class="modal-student-parent">
            <div class="modal-student">
              <div v-for="(item, index) in raisehandStudent" :key="item.userId" class="student-item">
                <div
                  class="student-name"
                  :class="{'student-name-select': item.select}"
                  @click="onStage(index)"
                >
                  <span>{{item.name}}</span>
                </div>
                <div class="student-hover">点击学生上台</div>
              </div>
            </div>
          </div>
        </div>
        <div class="finish-raisehand" @click="finishRaisehand">结束举手</div>
      </div>
    </div>
    <el-dialog title="答题卡" :visible.sync="answerCardVisible" class="answer-card" width="392px">
      <div class="el-dialog-content">
        <div class="dialog-choice">
          <div class="item1">选项</div>
          <div class="item2">
            <el-radio v-model="choice" label="1">单选题</el-radio>
            <el-radio v-model="choice" label="2">多选题</el-radio>
          </div>
        </div>
        <div class="dialog-option">
          <div class="item1">选项数</div>
          <div class="item2">
            <el-radio v-model="option" label="2">2</el-radio>
            <el-radio v-model="option" label="3">3</el-radio>
            <el-radio v-model="option" label="4">4</el-radio>
            <el-radio v-model="option" label="5">5</el-radio>
            <el-radio v-model="option" label="6">6</el-radio>
          </div>
        </div>
        <div class="dialog-answer">
          <div class="item1">答案</div>
          <div class="item2">
            <el-checkbox-group v-model="checkboxAnswer" v-show="choice == 2">
              <el-checkbox-button v-for="value in answers" :key="value+'checbox'" :label="value">{{value}}</el-checkbox-button>
              <!-- <el-checkbox-button label="B">B</el-checkbox-button>
              <el-checkbox-button label="C">C</el-checkbox-button>
              <el-checkbox-button label="D">D</el-checkbox-button>
              <el-checkbox-button label="E">E</el-checkbox-button>
              <el-checkbox-button label="F">F</el-checkbox-button> -->
            </el-checkbox-group>
            <el-radio-group v-model="radioAnswer" v-show="choice == 1">

              <el-radio-button v-for="value in answers" :key="value+'radio'" :label="value"></el-radio-button>
              <!-- <el-radio-button label="B"></el-radio-button>
              <el-radio-button label="C"></el-radio-button>
              <el-radio-button label="D"></el-radio-button>
              <el-radio-button label="E"></el-radio-button>
              <el-radio-button label="F"></el-radio-button> -->
            </el-radio-group>
            <div class="no-answer" v-if="noAnswer">请选择正确答案</div>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <span @click="goAnswer" class="go-answer">开始答题</span>
        <!-- <el-button type="primary" @click="dialogVisible = false">删除</el-button> -->
      </span>
    </el-dialog>
    <div class="modal-answer" v-show="modalAnswerStatus" @click="vModal">
      <div class="modal-content" @click.stop>
        <img :src="suoxiaoImg" class="suoxiaoImg" @click="suoxiao" />
        <div class="header-tab">
          <div class="item item1" :class="{select: answerTab == 1}" @click="answerTab = 1">答题统计</div>
          <div class="item item2" :class="{select: answerTab == 2}" @click="answerTab = 2">答题明细</div>
        </div>
        <div class="modal-main">
          <div class="title">
            <div class="item1">
              <span class="span1">正确率:</span>
              <span class="span2">{{accuracy}}</span>
              <span class="span3">%</span>
            </div>
            <div class="item2">
              <span class="span1">已答题人数:</span>
              <span class="span2">{{answerStudentCount}}</span>
              <span class="span3">/{{number.length}}</span>
            </div>
          </div>
          <div class="progress" v-show="answerTab == 1">
            <div class="progress-item" v-for="item in answerDetail">
              <div
                class="item1"
                :style="{color:item.isAnswer?'#6FBD25':'','font-weight':item.isAnswer?'bold':'400'}"
              >{{item.option}}</div>
              <div class="item2">
                <el-progress
                  :percentage="item.accuracy"
                  :format="progressFormat"
                  :class="{'is-answer':item.isAnswer}"
                ></el-progress>
              </div>
              <div class="item3">
                <span class="span1">{{item.accuracy}}%</span>
                ({{item.number}}人)
              </div>
            </div>
          </div>
          <div class="answer-detail" v-show="answerTab == 2">
            <div class="detail-item" v-for="item in answerDetail">
              <div class="item1">
                <div
                  class="item-item1"
                  :style="{color:item.isAnswer?'#6FBD25':'','font-weight':item.isAnswer?'bold':'400'}"
                >{{item.option}}</div>
                <div class="item-item2">({{item.number}}人)</div>
              </div>
              <div class="item2">
                <span
                  class="item-item"
                  v-for="val in item.students"
                  :style="{color:item.isAnswer?'#6FBD25':'','font-weight':item.isAnswer?'bold':'400'}"
                >{{val.name}}</span>
              </div>
            </div>
          </div>
        </div>
        <div class="finish-raisehand" @click="finishAnswer">结束答题</div>
      </div>
    </div>
    <div class="modal-bg" v-show="modalBgStatus"></div>
  </div>
</template>

<script>
import ScreenBar from "./components/screenBar";
import CountDown from "./components/countDown";
import leftRowImg from "@/assets/image/left_row.png";
import rightRowImg from "@/assets/image/right_row.png";
import leftRowImg1 from "@/assets/image/left_row1.png";
import rightRowImg1 from "@/assets/image/right_row1.png";
import signImg from "@/assets/image/sign.png";
import signSelectImg from "@/assets/image/sign_select.png";
import raisehandImg from "@/assets/image/raisehand.png";
import raisehandSelectImg from "@/assets/image/raisehand_select.png";
import answerImg from "@/assets/image/answer.png";
import answerSelectImg from "@/assets/image/answer_select.png";
import suoxiaoImg from "@/assets/image/minimize@2x.png";
import shiftImg from "@/assets/image/switch.png";
import turnoffcameraImg from "@/assets/image/turnoffcamera.png";
import exitStageImg from "@/assets/image/exit_stage.png";
import stopImg from "@/assets/image/live_btn_turnincamera_normal@2x.png"
import noCourseImg from "@/assets/image/no-course.png"
import Chat from "./components/chat";
import Api from "@/api";

export default {
  name: "Room",
  data() {
    return {
      // coursewares: [
      //   // "https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3078898191,2318315632&fm=26&gp=0.jpg",
      //   // "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1597310054774&di=9e4459cf72d7d6e301f7bce5361ba4b6&imgtype=0&src=http%3A%2F%2Fa3.att.hudong.com%2F14%2F75%2F01300000164186121366756803686.jpg",
      //   // "https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=3892521478,1695688217&fm=26&gp=0.jpg",
      // ],
      coursewares: '',
      coursewareIndex: 0,
      leftRowImg,
      rightRowImg,
      leftRowImg1,
      rightRowImg1,
      signImg,
      signSelectImg,
      raisehandImg,
      raisehandSelectImg,
      answerImg,
      answerSelectImg,
      suoxiaoImg,
      shiftImg,
      turnoffcameraImg,
      exitStageImg,
      stopImg,
      noCourseImg,
      modalBgStatus: false,
      // noSignCount: 40,
      signNumber: [],
      // count: 60,
      signStatus: false,
      modalSignStatus: false,
      raisehandStatus: false,
      modalRaisehandStatus: false,
      raisehandCount: 0,
      number: [],
      raisehandStudent: [],
      stageStudent: [
        // { userId: 1, name: "张三", select: true },
      ],
      answerCardVisible: false,
      choice: "1",
      option: "4",
      answers: ['A','B','C','D'],
      checkboxAnswer: [],
      radioAnswer: "",
      noAnswer: false,
      answerStatus: false,
      modalAnswerStatus: false,
      answerTab: 1, //1 答题统计 2 答题明细
      accuracy: 100, //正确率
      answerStudentCount: 0, //已答题的学生人数
      startCameraStatus: true,
      answerDetail: [
        // {
        //   option: "A",
        //   number: 5,
        //   accuracy: 20,
        //   isAnswer: true,
        //   students: [
        //     { id: 1, name: "张三" },
        //     { id: 2, name: "李四" },
        //     { id: 1, name: "张三" },
        //     { id: 2, name: "李四" },
        //     { id: 1, name: "张三嗷嗷" },
        //     { id: 2, name: "李四啊啊啊" },
        //     { id: 1, name: "张三三" },
        //     { id: 2, name: "李四" },
        //     { id: 1, name: "张三" },
        //     { id: 2, name: "李四" },
        //     { id: 1, name: "张三" },
        //     { id: 2, name: "李四" },
        //   ],
        // },
      ],
      teacherName: "",
      shiftStatus: false,
      bigPlayerWidth: 0,
      bigPlayerHeight: 0,

      //RTC
      rtc: null,
      cameraId: 0,
      userConfig: {
        app: "rtc",
        enterRoomTimeout: 15000000,
        connectPeerTimeout: 150000000,
        getEdgeTimeout : 15000000,
        imageWidth: 640,
        imageHeight: 480,
        width: 480,
        height: 270,
        fps: 15,
        sampleRate: 48000,
        channels: 2,
        crf: 27,
        roomId: "10000",
        userId: "123",
        token: "token",
      },
      previewer: null,
      //RTC end
      userId: '',
      chatId: '',
      courseId: '',
      // IM
      client: null,
      endTime:0,
      handUpBegin: {
        activity_id: ''
      },
      startCheckIn: {
        activity_id: ''
      },
      startAnswer: {
        activity_id: ''
      },
    };
  },
  components: {
    ScreenBar,
    CountDown,
    Chat,
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      let that = this
      window.onresize = function() {
        that.onResize()
      }
      console.log("query", this.$route.query)
      console.log("room monted")
      let chatId = this.$route.query.chatId
      this.chatId = chatId.toString()
      this.courseId = this.$route.query.courseId
      this.endTime = this.$route.query.endTime
      Api.User.userInfo().then((val) => {
        this.userId = val.id
        this.teacherName = val.nickname
        this.imLogin()
        this.getUsersByChatId(chatId)
        this.createRTC()
      })
      let course_id = this.$route.query.courseId
      let obj = {
        course_id: course_id,
        list_type: 'all'
      }
      Api.Course.coursewareList(course_id,{params:obj}).then(val => {
        this.coursewares = val.list[0]
        // if (val.count == 0 ) {
        //   that.checkCourseVisible = false
        //   setTimeout(() => {
        //     that.coursewares = []
        //     that.coursewareIndex = 0
        //   }, 0);
        //   this.getCourse(0)
        // } else {
        //   that.coursewares = val.list
        // }
      }).catch((err) => {

      })
    },
    switchImg(type) {
      if (type == "left") {
        if (this.coursewareIndex > 0) {
          this.coursewareIndex--
          this.sendMessage('turnPage', {pageIndex: this.coursewareIndex})
        }
      }
      if (type == "right") {
        if (this.coursewareIndex < this.coursewares.images.length - 1) {
          this.coursewareIndex++
          this.sendMessage('turnPage', {pageIndex: this.coursewareIndex})
        }
      }
    },
    startSign() {
      this.modalBgStatus = true
      this.modalSignStatus = true
      if (this.signStatus) {
        return
      }
      this.signStatus = true
      this.sendMessage('startCheckIn')
    },
    startAnswer_() {
      // this.modalBgStatus = true
      if (this.answerStatus) {
        this.modalBgStatus = true
        this.modalAnswerStatus = true
      } else {
        this.answerCardVisible = true
      }
    },
    startRaisehand() {
      this.modalBgStatus = true;
      this.modalRaisehandStatus = true;
      if (this.raisehandStatus) {
        return
      }
      this.raisehandStatus = true
      this.sendMessage('handUpBegin')
    },
    finishSign() {
      this.signStatus = false
      this.modalBgStatus = false
      this.modalSignStatus = false
      this.sendMessage('endCheckIn', {activity_id: this.startCheckIn.activity_id})
      this.signNumber = []
    },
    suoxiao() {
      this.modalBgStatus = false
      this.modalSignStatus = false
      this.modalRaisehandStatus = false
      this.modalAnswerStatus = false
    },
    vModal() {
      this.modalBgStatus = false
      this.modalSignStatus = false
      this.modalRaisehandStatus = false
      this.modalAnswerStatus = false
    },
    finishRaisehand() {
      this.raisehandStatus = false
      this.modalBgStatus = false
      this.modalRaisehandStatus = false
      this.sendMessage('handUpEnd',{activity_id: this.handUpBegin.activity_id})
      let stageStudent = this.stageStudent
      stageStudent = JSON.parse(JSON.stringify(stageStudent))
      this.raisehandStudent = stageStudent
    },
    onStage(index) {
      let obj = this.raisehandStudent[index]
      if (obj.select) {
        return
      }
      obj.select = true
      //上台
      let status = false
      this.stageStudent.forEach(item => {
        if (item.userId == obj.userId) {
          status = true
        }
      })
      if (!status) {
        this.stageStudent.push(obj)
      }
      console.log("obj this.handUpBegin", obj, index,this.handUpBegin)
      this.$set(this.raisehandStudent, index, obj)
      this.sendMessage('handUpSelected',{userId: obj.userId})
    },
    goAnswer() {
      if (this.choice == 1 && !this.radioAnswer) {
        this.noAnswer = true;
        return;
      }
      if (this.choice == 2 && this.checkboxAnswer.length == 0) {
        this.noAnswer = true;
        return;
      }
      this.answerStatus = true;
      this.answerCardVisible = false;
      this.modalAnswerStatus = true;
      let params = {
        type: this.choice,
        select: this.answers
      }
      if (this.choice == 1) {
        params.answer = [this.radioAnswer]
      } else {
        params.answer = this.checkboxAnswer
      }
      console.log('this.checkboxAnswer',this.checkboxAnswer,this.radioAnswer)
      this.sendMessage('startAnswer', params)
    },
    progressFormat(percentage) {
      return "";
    },
    finishAnswer() {
      this.answerStatus = false
      this.modalBgStatus = false
      this.modalAnswerStatus = false
      this.accuracy = 0
      this.answerStudentCount = 0
      this.answerDetail = []
      this.checkboxAnswer = []
      this.choice = '1'
      this.option = '4'
      this.answers = ['A','B','C','D'],
      this.radioAnswer = ''
      console.log('finishAnswer')
      this.sendMessage('endAnswer',{activity_id: this.startAnswer.activity_id})
    },
    shiftPlayerScreen() {
      this.shiftStatus = !this.shiftStatus;
    },
    exitStage(userId) {
      let stageStudent = [];
      this.stageStudent.forEach((item) => {
        if (item.userId != userId) {
          stageStudent.push(item);
        }
      });
      let index;
      let obj = {};
      for (let i = 0; i < this.raisehandStudent.length; i++) {
        if (this.raisehandStudent[i].userId == userId) {
          index = i;
          obj = this.raisehandStudent[i];
          break;
        }
      }
      obj.select = false;
      this.stageStudent = stageStudent;
      this.$set(this.raisehandStudent, index, obj)
      this.sendMessage('kickout',{userId: userId})
    },
    onResize() {
      if (this.shiftStatus) {
        let dom = this.$refs.courseware;
        let width = dom.offsetWidth
        let height = dom.offsetHeight
        
        let bigPlayerHeight = dom.offsetHeight - 203
        let bigPlayerWidth = (16 * bigPlayerHeight) / 9
        if (bigPlayerWidth > width) {
          bigPlayerWidth = width
          bigPlayerHeight = (9 * width) / 16
        }
        if (bigPlayerHeight > width - 203) {
          bigPlayerHeight =  width - 203
          bigPlayerWidth = (16 * bigPlayerHeight) / 9
        }
        this.bigPlayerHeight = bigPlayerHeight;
        this.bigPlayerWidth = bigPlayerWidth;
      }
    },
