            avatar: value.avatar,
            nickname: value.nickname,
            userId: value.id,
            forbidden: false
          }
          number.push(obj)
        })
        this.number = number
        console.log('this.number',number)
      })
    },
    getUserObj(userid) {
      let obj = {}
      for(let i = 0; i < this.number.length; i++) {
        if (this.number[i].userId == userid) {
          obj = this.number[i]
          break
        }
      }
      return obj
    },
    /*
     * RTC
     *
     *
     * */
    createRTC() {
      // var url = 'http://127.0.0.1:8001/getEdge';
      let url = "http://boss-tob-dev.dding.net:8001/getEdge";
      // let interval = setInterval(() => {
        
      // }, 100);
      let urtc = window.urtc
      if (urtc) {
        // clearInterval(interval)
        // interval = null
        let userConfig = this.userConfig
        userConfig.roomId = this.chatId.toString()
        userConfig.userId = this.userId.toString()
        userConfig.token = window.localStorage.getItem('access_token')
        if (this.rtc) {
          rtc.close()
          this.rtc = null
        }
        urtc.setDebugFmsUrl(url)
        let rtc = urtc.createRTC(userConfig)
        rtc.on("error", console.error)
        rtc.on("selfJoined", () => {
          console.info("selfJoined")
        })
        rtc.on("selfConnected", (success) => {
          console.info("selfConnected :", success)
        })
        rtc.on("selfConnectTimeout", () => {
          console.info("selfConnectTimeout")
        })
        rtc.on("selfOffline", () => {
          console.info("selfOffline")
        })
        rtc.on("selfLeaved", () => {
          console.info("selfLeaved")
        })
        rtc.on("statsResult", (stats) => {
          // console.info("statsResult :", stats)
        })
        rtc.on("peerJoined", (userId) => {
          console.info("peerJoined")
          let status = false
          this.stageStudent.forEach((item) => {
            if (item.userId == userId) {
              status = true
            }
          })
          if (!status) {
            let userObj = this.getUserObj(userId)
            let obj = {
              userId: userId,
              name: userObj.nickname,
              select: true,
            }
            this.stageStudent.push(obj)
            this.raisehandStudent.push(obj)
          }
          this.$nextTick(() => {
            this.addUser(this.chatId, userId, 'student-player-'+userId)
          })

        })
        rtc.on("peerConnected", (userId) => {
          console.info("peerConnected :", userId)
        })
        rtc.on("peerConnectTimeout", (userId) => {
          console.info("peerConnectTimeout :", userId)
        })
        rtc.on("peerOffline", (userId) => {
          console.info("peerOffline :", userId)
        })
        rtc.on("peerLeaved", (userId) => {
          console.info("peerLeaved :", userId)
          let status = false
          let data = []
          this.stageStudent.forEach((item) => {
            if (item.userId == userId) {
              status = true
            } else {
              data.push(item)
            }
          })
          this.stageStudent = data
          status = false
          let data1 = []
          this.raisehandStudent.forEach((item) => {
            if (item.userId == userId) {
              status = true
            } else {
              data1.push(item)
            }
          })
          this.raisehandStudent = data1
        })
        this.rtc = rtc;
        this.joinRoom()
      }
    },
    joinRoom() {
      let userConfig = this.userConfig;
      userConfig.roomId = this.chatId.toString();
      userConfig.userId = this.userId.toString();
      userConfig.token = window.localStorage.getItem('access_token')
      let rtc = this.rtc
      if (rtc) {
        let ret = rtc.joinRoom(userConfig)
      }
      if (!this.shiftStatus) {
        setTimeout(() => {
          this.startCamera("teacher-player")
        }, 0)
      } else {
        setTimeout(() => {
          this.startCamera("big-teacher-player")
        }, 0)
      }
    },
    startCamera(canvasId) {
      let rtc = this.rtc
      if (rtc) {
        let cameraId = this.cameraId
        if (cameraId <= 0) {
          cameraId = rtc.startCamera(0, 320, 240, 15)
        }
        let previewer = this.previewer
        if (!previewer) {
          previewer = rtc.addRenderer(cameraId)
          this.previewer = previewer
        }
        let canvas = document.getElementById(canvasId)
        previewer.attachCanvas(canvas)
        previewer.startRender({mirrorY : true})

        this.cameraId = cameraId
        this.publish()
      }
    },
    startCamera_() {
      this.startCameraStatus = true
      let params = {
        userId: this.userId
      }
      this.sendMessage('openCamera', params)
      if (!this.shiftStatus) {
        setTimeout(() => {
          this.startCamera("teacher-player")
        }, 0)
      } else {
        setTimeout(() => {
          this.onResize()
          setTimeout(() => {
            this.startCamera("big-teacher-player")
          }, 0)
        }, 0);
      }
    },
    closeCamera() {
      // this.stopAll()
      let rtc = this.rtc
      let cameraId = this.cameraId
      rtc.stopCamera(cameraId)
      this.cameraId = 0
      this.startCameraStatus = false
      this.previewer.close()
      this.previewer = null
      let params = {
        userId: this.userId
      }
      this.sendMessage('closeCamera', params)
      // if (this.previewer) {
      //   // this.previewer.close()
      //   this.rtc.close()
      //   this.rtc = null
      //   this.previewer = null
      //   this.cameraId = null
      // } else {
      //   return
      // }
    },
    publish() {
      console.log('public',this.rtc)
      let rtc = this.rtc
      let cameraId = this.cameraId
      let userConfig = this.userConfig
      userConfig.roomId = this.chatId.toString()
      userConfig.userId = this.userId.toString()
      userConfig.token = window.localStorage.getItem('access_token')
      if (rtc) {
        rtc.attachMedia(cameraId, userConfig)
        if (cameraId > 0) {
          rtc.sendVideoData(cameraId)
        }
      }
    },
    addUser(roomId, userId, canvasId) {
      let rtc = this.rtc
      if (rtc) {
        let previewer = rtc.addUser(roomId, userId) //房间号--userid
        if (previewer) {
          let canvas = document.getElementById(canvasId)
          previewer.attachCanvas(canvas)
          previewer.startRender()
        }
      }
    },
    leaveRoom() {
      let rtc = this.rtc
      if (rtc) {
        rtc.leaveRoom()
      }
    },
    stopAll() {
      let rtc = this.rtc
      if (rtc) {
        rtc.close()
        this.rtc = null
        this.previewer = null
        this.cameraId = null
      }
    },
    /*
     * IM
     */
    imLogin() {
      if (this.client && this.client.connected) {
        return
      }
      let userId = this.userId
      console.log('imlogin userid',userId)
      this.client = window.uimsdk.createClient({
        // url: "rtmp://boss-tob-dev.dding.net:444/im",
        url: "rtmp://boss-tob-dev.dding.net:448/im",
        // url : 'rtmp://127.0.0.1:4444/im',
        // dbPath: 'C:/work/electron-quick-start/dist/data/8.db',
        dbDath:window.path.resolve(__dirname,`../data/${userId}.db`),
        sendTimeout: 5000,
        userId: parseInt(userId),
      });
      this.client.login({
        userId: parseInt(userId),
        access_token: window.localStorage.getItem('access_token'),
      })
      // this.client.on("send", (result) => {
      //   console.info("user[%d] send :", this.userId, result);
      // });
      let that = this
      this.client.on("login", () => {
        that.client.call('/im/v1/listChatActivity', JSON.stringify({
          chat_id: that.chatId
        }), (response)=>{
            console.info('call response :', JSON.parse(response))
            response = JSON.parse(response)
            if (response.list.length > 0) {
              response.list.forEach((item) => {
                let status = false
                let obj = {}
                switch(item.type) {
                  case 1:
                    if (item.extension) {
                      that.coursewareIndex = item.extension.pageIndex
                      that.sendMessage('turnPage', {pageIndex: item.extension.pageIndex})
                    }
                    break
                  case 2: //举手
                    that.raisehandStatus = true
                    that.sendMessage('handUpBegin')
                    that.handUpBegin =  {
                      activity_id: item.id
                    }
                    break
                  case 3: //签到
                    that.signStatus = true
                    that.startCheckIn = {
                      activity_id: item.id
                    }
                    that.getActivityStats(item.id, 3)
                    break
                  case 4: //答题
                    that.answerStatus = true
                    that.startAnswer = {
                      activity_id: item.id
                    }
                    if (item.extension) {
                      that.choice = item.extension.type
                      that.option = item.extension.select.length
                      that.answers = item.extension.select
                      if (item.extension.type == 1) {
                        that.radioAnswer = item.extension.answer[0]
                      } else {
                        that.checkboxAnswer = item.extension.answer
                      }
                    }
                    that.getActivityStats(item.id, 4)
                    break
                  case 5: //上台
                    status = false
                    that.raisehandStudent.forEach(item1 => {
                      if (item.extension.userId == item1.userId) {
                        status = true
                        item1.select = true
                      }
                    })
                    if (!status) {
                      obj = {
                        userId: item.extension.userId,
                        select: true,
                        name: that.getUserObj(item.extension.userId).nickname
                      }
                      that.raisehandStudent.push(obj)
                      that.stageStudent.push(obj)
                    }
                    break
                }
              })
            }
        });
      })
      this.client.on("logout", () => {
        console.info("user[%d] is logged out.", this.userId)
      });
      this.client.on("offline", () => {
        console.info("user[%d] is offline.", this.userId)
      })
      this.client.on("close", () => {
        console.info("user[%d] closed.", this.userId)
      })
      this.client.on("message", (messages) => {
        console.info("user[%d] recv message :", this.userId, messages);
        let msgIds = [];
        for (let i = 0; i < messages.length; i++) {
          const msg = messages[i];
          msgIds.push(messages[i].id);
        }
        let msgs = this.client.getMessageById(msgIds);
        console.log('onmessage',msgs)
        for (let i = 0; i < msgs.length; i++) {
          //
          // this.appendMessage(msgs[i].user_id, msgs[i].content);
          if (msgs[i].type == 1) {
            let obj = this.getUserObj(msgs[i].user_id || '')
            let extension = msgs[i].extension
            extension = JSON.parse(extension)
            let obj1 = {}
            let status
            switch(extension.name) {
              case 'handUp':
                obj1 = {
                  name: obj.nickname,
                  userId: obj.userId,
                  select: false
                }
                status = false
                this.raisehandStudent.forEach(item => {
                  if (item.userId == obj1.userId) {
                    status = true
                  }
                })
                if (!status) {
                  this.raisehandStudent.push(obj1)
                }
                break
              case 'handDown':
                let raisehandStudent = this.raisehandStudent
                raisehandStudent = JSON.parse(JSON.stringify(raisehandStudent))
                let data = []
                raisehandStudent.forEach((item) => {
                  if (obj.userId != item.userId) {
                    data.push(item)
                  }
                })
                let data_ = []
                let stageStudent = JSON.parse(JSON.stringify(this.stageStudent))
                this.stageStudent.forEach((item) => {
                  if (item.userId != obj.userId) {
                    data_.push(item)
                  }
                })
                this.stageStudent = data_
                this.raisehandStudent = data
                break
              case 'checkIn':
                obj1 = {
                  name: obj.nickname,
                  userId: obj.userId,
                  select: false
                }
                this.signNumber.forEach(item => {
                  if (item.userId == obj1.userId) {
                    status = true
                  }
                })
                if (!status) {
                  this.signNumber.push(obj1)
                }
                break
              case 'answerQuestion':
                console.log('extension ===',extension)
              default :
            }
          }
        }
      });
      this.client.on("send", (result) => {
        console.info("user[%d] send :", this.userId, result);
        if (result && result.messages && result.messages[0]) {
          let message = result.messages[0]
          console.log('message',message)
          if (message.type == 1) {
            let extension = JSON.parse(message.extension) 
            switch(extension.name) {
              case 'handUpBegin':
                this.handUpBegin = {
                  activity_id: extension.data.activity_id
                }
                console.log('this.handUpBegin',this.handUpBegin)
                break
              case 'startAnswer':
                this.startAnswer = {
                  activity_id: extension.data.activity_id
                }
                break
              case 'startCheckIn':
                this.startCheckIn = {
                  activity_id: extension.data.activity_id
                }
                break
            }
          }
        }
        // let message = result.messages[0].content
        // let obj = this.getUserObj(this.userId)
        // obj = JSON.parse(JSON.stringify(obj))
        // obj.messageId = result.messages[0].id
      });
      this.client.on('stats', (result) => {
        console.info("user[%d] stats :", this.userId, result);
        // type this.choice,
        // select this.answers
        // this.radioAnswer
        // answerDetail = [
        // {
        //   option: "A",
        //   number: 5,
        //   accuracy: 20,
        //   isAnswer: true,
        //   students: [
        //     { id: 1, name: "张三" },
        //     { id: 2, name: "李四" },
        //     { id: 1, name: "张三" },
        //     { id: 2, name: "李四" },
        //     { id: 1, name: "张三嗷嗷" },
        //     { id: 2, name: "李四啊啊啊" },
        //     { id: 1, name: "张三三" },
        //     { id: 2, name: "李四" },
        //     { id: 1, name: "张三" },
        //     { id: 2, name: "李四" },
        //     { id: 1, name: "张三" },
        //     { id: 2, name: "李四" },
        //   ],
        // },
        //  ]
        let args = result.args[0]
        if (args.type != 4) {
          return
        }
        this.answerStats(args)
      })
    },
    imClose() {
      if (this.client) {
        this.client.close()
        this.client = null
      }
    },
    sendMessage(name,data = {}) {
      console.log('chatIDddd',this.chatId, name,data)
      if (this.client) {
        let chatId = this.chatId
        chatId = parseInt(chatId) 
        this.client.sendMessage(chatId, {
          type: 1,
          message: '',
          extension: JSON.stringify({
            name: name,
            data: data
          }),
        })
      }
    },
    //
    getActivityStats(activityId,type) {
      this.client.call('/im/v1/getActivityStats', JSON.stringify({
        activity_id: activityId
      }), (response) => {
        response = JSON.parse(response)
        let data = []
        switch(type) {
          case 3: //签到
            data = []
            response.list.forEach(item => {
              let obj = {
                name: item.nickname,
                userId: item.userId,
              }
              data.push(obj)
            })
            this.signNumber = data
            break
          case 4: //答题
            this.answerStats(response)
            break
        } 
        console.log('call response stats', response)
      })
    },
    //
    answerStats(args) {
      if (args.list.length == 0) {
        return
      }
      let answerDetail = []
      let accuracy1 = args.right.length / args.count * 100
      accuracy1 = Math.round((accuracy1 + Number.EPSILON) * 100) / 100
      this.accuracy = accuracy1
      this.answerStudentCount = args.count
      this.answers.forEach(item => {
        let obj = {
          option: item
        }
        if (this.choice == 1) {
          if (item == this.radioAnswer) {
            obj.isAnswer = true
          } else {
            obj.isAnswer = false
          }
          let number = args.aggregate[item] || []
          obj.number = number.length
          let accuracy = number.length / args.count * 100
          accuracy = Math.round((accuracy + Number.EPSILON) * 100) / 100
          obj.accuracy = accuracy
          obj.students = []
          if (args.aggregate[item]) {
            args.aggregate[item].forEach(item1 => {
              let studentObj = {}
              for(let i = 0; i < args.list.length; i++) {
                if (args.list[i].id == item1) {
                  studentObj = args.list[i]
                  break
                }
              }
              let obj1 = {
                id: item1,
                name: studentObj.nickname
              }
              obj.students.push(obj1)
            }) 
          }
          answerDetail.push(obj)
        } else { //多选
          if (this.checkboxAnswer.indexOf(item) != -1) {
            obj.isAnswer = true
          } else {
            obj.isAnswer = false
          }
          let number = 0
          obj.students = []
          for(let key in args.aggregate) {
            let data = key.split(',')
            if (data.indexOf(item) != -1) {
              number += args.aggregate[key].length
              args.aggregate[key].forEach(item1 => {
                let studentObj = {}
                for(let i = 0; i < args.list.length; i++) {
                  if (args.list[i].id == item1) {
                    studentObj = args.list[i]
                    break
                  }
                }
                let obj1 = {
                  id: item1,
                  name: studentObj.nickname
                }
                obj.students.push(obj1)
              }) 
            }
          }
          obj.number = number
          let accuracy = number / args.count * 100
          accuracy = Math.round((accuracy + Number.EPSILON) * 100) / 100
          obj.accuracy = accuracy
          answerDetail.push(obj)
        }
      })
      this.answerDetail = answerDetail
      console.log('answerDetail',answerDetail)
    }
  },
  watch: {
    radioAnswer(val) {
      if (this.choice == 1 && val) {
        this.noAnswer = false;
      }
    },
    checkboxAnswer(val) {
      if (this.choice == 2 && val.length > 0) {
        this.noAnswer = false;
      }
    },
    shiftStatus(val) {
      if (this.startCameraStatus) {
        if (val) {
          setTimeout(() => {
            this.onResize()
            setTimeout(() => {
              this.startCamera("big-teacher-player");
            }, 0);
          }, 0);
        } else {
          this.bigPlayerHeight = 0;
          this.bigPlayerWidth = 0;
          setTimeout(() => {
            this.startCamera("teacher-player");
          }, 0);
        }
      }
    },
    option(val) {
      if (val) {
        switch(val) {
          case '2':
            this.answers = ['A','B']
            break
          case '3':
            this.answers = ['A','B','C']
            break
          case '4':
            this.answers = ['A','B','C','D']
            break
          case '5':
            this.answers = ['A','B','C','D','E']
            break
          case '6':
            this.answers = ['A','B','C','D','E', 'F']
            break
          default:
        }
      }
      
    }
  },
  beforeDestroy() {
    window.onresize = null;
    this.stopAll()
    this.imClose()
  },
};
</script>

