            transform: translate(-50%,-50%);
            .start-camera-item {
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%,-50%);
              cursor: pointer;
              .span1 {
                position: absolute;
                top: 0px;
                img {
                  width: 15px;
                }
              }
              .span2 {
                font-size: 12px;
                font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
                font-weight: 400;
                color: #FFFFFF;
                margin-left: 16px;
              }
            }
          }
        }
        .student-on-stage {
          bottom: 0;
          transform: none;
          top: auto;
        }
      }
      .content-left-bottom {
        display: flex;
        justify-content: center;
        width: 72%;
        margin-top: 10px;
        .bottom-item {
          display: flex;
          flex-direction: column;
          text-align: center;
          margin-right: 24px;
          cursor: pointer;
          img {
            width: 60px;
            height: 30px;
          }
          span {
            font-size: 10px;
            font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
            font-weight: 400;
            color: rgba(167, 167, 167, 1);
            margin-top: 6px;
          }
        }
      }
    }
    .content-right {
      width: 300px;
      height: calc(100% - 100px);
      position: absolute;
      background: rgba(54, 58, 73, 1);
      right: 0;
      top: 53px;
      .player {
        width: 272px;
        height: 153px;
        background: #2c3e50;
        border-radius: 6px;
        margin: auto;
        margin-top: 16px;
        position: relative;
        canvas {
          // width: 100%;
          // height: 100%;
          border-radius: 6px;
        }
        .courseware {
          width: 100%;
          height: 100%;
          position: relative;
          text-align: center;
          background: white;
          border-radius: 6px;
          .course-img {
            // transform: translate(0px, -50%);
            position: absolute;
            // top: 50%;
            top: 6px;
            width: 100%;
            img.courseware-img {
              height: 120px;
            }
            .jiantou {
              margin-top: 0px;
              // margin-bottom: 10px;
              .left-row {
                margin-right: 15px;
              }
              img {
                width: 20px;
              }
            }
          }
          .no-course {
            background: #585867;
            transform: translate(0px, -50%);
            text-align: center;
            position: absolute;
            top: 50%;
            width: 100%;
            // height: 408px;
            height: 100%;
            border-radius: 6px;
            .no-course-content {
              position: absolute;
              width: 100%;
              top: 50%;
              transform: translate(0, -50%);
              .yuan {
                // width: 120px;
                // height: 120px;
                // border-radius: 50%;
                // background: rgba(109, 109, 125, 1);
                // margin-left: calc(50% - 60px);
                img {
                  width: 45px;
                }
              }
              .bottom-item {
                margin-top: 12px;
                font-size: 12px;
                font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
                font-weight: 400;
                color: rgba(255, 255, 255, 1);
              }
            }
          }
        }
        .teacher-name {
          font-size: 14px;
          font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
          font-weight: 400;
          color: rgba(255, 255, 255, 1);
          position: absolute;
          left: 10px;
          bottom: 8px;
        }
        .start-camera {
          width: 100%;
          height: 100%;
          background: #5E95F4;
          border-radius: 6px;
          .start-camera-item {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            cursor: pointer;
            .span1 {
              position: absolute;
              top: 2px;
              img {
                width: 15px;
              }
            }
            .span2 {
              font-size: 12px;
              font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
              font-weight: 400;
              color: #FFFFFF;
              margin-left: 16px;
            }
          }
        }
        .shift-img {
          width: 24px;
          height: 24px;
          position: absolute;
          right: 4px;
          bottom: 4px;
          cursor: pointer;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .turnoffcamera-img {
          width: 24px;
          height: 24px;
          position: absolute;
          cursor: pointer;
          right: 4px;
          bottom: 32px;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
      .content-chat {
        height: calc(100% - 179px);
        margin-top: 10px;
      }
    }
  }
  .modal-bg {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    background: #000;
    z-index: 2003;
  }
  .modal-sign {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
    margin: 0;
    z-index: 2023;
    .modal-sign-content {
      width: 319px;
      height: 287px;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 7px 19px 1px rgba(83, 93, 110, 0.28);
      border-radius: 6px;
      margin: 0 auto;
      margin-top: 15vh;
      position: relative;
      .modal-sign-header {
        font-size: 16px;
        font-family: AlibabaPuHuiTi-Medium, AlibabaPuHuiTi;
        font-weight: 600;
        color: rgba(50, 50, 50, 1);
        padding-top: 30px;
        margin-left: 18px;
      }
      .suoxiaoImg {
        width: 20px;
        position: absolute;
        right: 32px;
        top: 20px;
        cursor: pointer;
      }
      .modal-sign-main {
        margin-top: 45px;
        .modal-sign-main-item {
          font-size: 14px;
          font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
          font-weight: 400;
          color: rgba(50, 50, 50, 1);
          margin-left: 29px;
        }
        .modal-sign-main-item2 {
          display: flex;
          justify-content: space-between;
          width: 259px;
          text-align: center;
          margin: auto;
          margin-top: 30px;
          .item {
            .count {
              font-size: 20px;
              font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
              font-weight: 400;
              color: rgba(50, 50, 50, 1);
            }
            .no-sign-count {
              font-weight: 600;
              color: rgba(94, 149, 244, 1);
            }
            .content {
              font-size: 12px;
              font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
              font-weight: 400;
              color: rgba(50, 50, 50, 1);
              margin-top: 6px;
            }
          }
        }
      }
      .finish-sign {
        width: 100px;
        height: 30px;
        background: rgba(94, 149, 244, 1);
        border-radius: 4px;
        font-size: 14px;
        font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
        font-weight: 400;
        color: rgba(255, 255, 255, 1);
        line-height: 30px;
        text-align: center;
        right: 20px;
        margin-top: 40px;
        position: absolute;
        cursor: pointer;
        margin-right: 10px;
      }
    }
  }
  .modal-raisehand {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
    margin: 0;
    z-index: 2023;
    .modal-content {
      width: 893px;
      height: 421px;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 7px 19px 1px rgba(83, 93, 110, 0.28);
      border-radius: 6px;
      margin: 0 auto;
      margin-top: 15vh;
      position: relative;
      .modal-header {
        font-size: 16px;
        font-family: AlibabaPuHuiTi-Medium, AlibabaPuHuiTi;
        font-weight: 600;
        color: rgba(50, 50, 50, 1);
        padding-top: 30px;
        margin-left: 18px;
      }
      .suoxiaoImg {
        width: 20px;
        position: absolute;
        right: 32px;
        top: 20px;
        cursor: pointer;
      }
      .modal-main {
        width: 833px;
        margin: auto;
        margin-top: 35px;
        .modal-title {
          font-size: 14px;
          font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
          font-weight: 400;
          color: rgba(50, 50, 50, 1);
          .raisehand-count {
            color: #5e95f4;
            font-size: 20px;
            position: absolute;
            margin-top: -6px;
            font-weight: 600;
          }
          .all-count {
            position: absolute;
            margin-left: 11px;
          }
        }
        .modal-student-parent {
          height: 200px;
          overflow-y: scroll;
          margin-top: 20px;
          .modal-student {
            display: flex;
            flex-wrap: wrap;
            .student-item {
              position: relative;
              width: 70px;
              height: 30px;
              margin-right: 10px;
              margin-bottom: 14px;
              .student-name {
                width: 70px;
                height: 30px;
                border-radius: 4px;
                border: 1px solid rgba(239, 239, 239, 1);
                line-height: 30px;
                text-align: center;
                font-size: 14px;
                font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
                font-weight: 400;
                color: rgba(50, 50, 50, 1);
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                cursor: pointer;
                &:hover {
                  & + .student-hover {
                    display: block;
                  }
                  & {
                    border: 1px solid rgba(94, 149, 244, 1);
                    color: #5e95f4;
                  }
                }
              }
              .student-name-select {
                background: rgba(94, 149, 244, 1);
                color: white !important;
              }
              .student-hover {
                position: absolute;
                width: 80px;
                height: 31px;
                background: rgba(255, 255, 255, 1);
                box-shadow: 0px 1px 9px 1px rgba(98, 104, 115, 0.34);
                border-radius: 6px;
                border: 1px solid rgba(167, 167, 167, 1);
                line-height: 31px;
                font-size: 10px;
                font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
                font-weight: 400;
                color: rgba(167, 167, 167, 1);
                text-align: center;
                z-index: 1;
                display: none;
                left: 73px;
                top: 0px;
              }
            }
          }
        }
        ::-webkit-scrollbar {
          display: none; /* Chrome Safari */
        }
      }
      .finish-raisehand {
        width: 100px;
        height: 30px;
        background: rgba(94, 149, 244, 1);
        border-radius: 4px;
        font-size: 14px;
        font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
        font-weight: 400;
        color: rgba(255, 255, 255, 1);
        line-height: 30px;
        text-align: center;
        position: absolute;
        right: 30px;
        bottom: 20px;
        cursor: pointer;
      }
    }
  }
  .answer-card {
    /deep/.el-dialog__header {
      margin-bottom: 41px;
      .el-dialog__title {
        position: absolute;
        top: 29px;
        font-size: 16px;
        font-family: AlibabaPuHuiTi-Medium, AlibabaPuHuiTi;
        font-weight: 600;
        color: rgba(50, 50, 50, 1);
      }
      // .el-dialog__headerbtn {
      //   font-size: 29px;
      //   i {
      //     color: #989898;
      //   }
      //   &:hover {
      //     i {
      //       color: #989898;
      //     }
      //   }
      // }
    }
    /deep/.el-dialog__body {
      padding-left: 30px;
      .el-dialog-content {
        .dialog-choice {
          position: relative;
          .item1 {
            font-size: 14px;
            font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
            font-weight: 400;
            color: rgba(50, 50, 50, 1);
            position: absolute;
            left: 13px;
            top: -1px;
          }
          .item2 {
            margin-left: 71px;
            .el-radio {
              margin-right: 18px;
            }
          }
        }
        .dialog-option {
          position: relative;
          margin-top: 26px;
          .item1 {
            font-size: 14px;
            font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
            font-weight: 400;
            color: rgba(50, 50, 50, 1);
            position: absolute;
            left: 0px;
            top: -1px;
          }
          .item2 {
            margin-left: 71px;
            .el-radio {
              margin-right: 18px;
            }
          }
        }
        .dialog-answer {
          position: relative;
          margin-top: 22px;
          .item1 {
            font-size: 14px;
            font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
            font-weight: 400;
            color: rgba(50, 50, 50, 1);
            position: absolute;
            left: 13px;
            top: 5px;
          }
          .item2 {
            margin-left: 71px;
            .el-checkbox-group {
              .el-checkbox-button {
                .el-checkbox-button__inner {
                  padding: 0;
                  width: 30px;
                  height: 30px;
                  text-align: center;
                  line-height: 30px;
                  border-radius: 6px !important;
                  margin-right: 16px;
                  border: 1px solid rgba(239, 239, 239, 1);
                  border-left: 1px solid rgba(239, 239, 239, 1);
                  box-shadow: none;
                }
                &:last-child {
                  .el-checkbox-button__inner {
                    margin-right: 0px;
                  }
                }
              }
            }
            .el-radio-group {
              .el-radio-button {
                .el-radio-button__inner {
                  padding: 0;
                  width: 30px;
                  height: 30px;
                  text-align: center;
                  line-height: 30px;
                  border-radius: 6px !important;
                  margin-right: 16px;
                  border: 1px solid rgba(239, 239, 239, 1);
                  border-left: 1px solid rgba(239, 239, 239, 1);
                  box-shadow: none;
                }
                &:last-child {
                  .el-radio-button__inner {
                    margin-right: 0px;
                  }
                }
              }
            }
            .no-answer {
              position: absolute;
              font-size: 10px;
              font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
              font-weight: 400;
              color: rgba(245, 96, 96, 1);
              margin-top: 4px;
            }
          }
        }
      }
    }
    .dialog-footer {
      width: 100px;
      height: 30px;
      background: rgba(94, 149, 244, 1);
      border-radius: 4px;
      text-align: center;
      line-height: 30px;
      cursor: pointer;
      display: inline-block;
      font-size: 14px;
      font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
      font-weight: 400;
      color: rgba(255, 255, 255, 1);
    }
  }
  /deep/.el-dialog {
    border-radius: 6px;
  }
  .modal-answer {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
    margin: 0;
    z-index: 2023;
    .modal-content {
      width: 462px;
      height: 438px;
      background: rgba(255, 255, 255, 1);
      box-shadow: 0px 7px 19px 1px rgba(83, 93, 110, 0.28);
      border-radius: 6px;
      margin: 0 auto;
      margin-top: 15vh;
      position: relative;
      .suoxiaoImg {
        width: 20px;
        position: absolute;
        right: 32px;
        top: 20px;
        cursor: pointer;
      }
      .header-tab {
        font-size: 14px;
        font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
        font-weight: 400;
        color: rgba(167, 167, 167, 1);
        display: flex;
        text-align: center;
        justify-content: center;
        padding-top: 30px;
        .item {
          width: 80px;
          height: 30px;
          background: #fafbfb;
          line-height: 30px;
          cursor: pointer;
          &.select {
            background: rgba(245, 247, 248, 1);
            font-weight: bold;
            color: rgba(94, 149, 244, 1);
          }
        }
        .item1 {
          border-radius: 4px 0px 0px 4px;
        }
        .item2 {
          border-radius: 0px 4px 4px 0px;
        }
      }
      .modal-main {
        margin-left: 30px;
        margin-top: 36px;
        margin-right: 30px;
        .title {
          display: flex;
          font-size: 14px;
          font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
          font-weight: 400;
          color: rgba(50, 50, 50, 1);
          .item1 {
            margin-right: 73px;
            position: relative;
            line-height: 25px;
            display: flex;
            .span2 {
              color: #5e95f4;
              font-size: 18px;
              font-weight: 600;
              margin-left: 1px;
            }
            .span3 {
              display: inline-block;
              line-height: 27px;
              margin-left: 1px;
            }
          }
          .item2 {
            position: relative;
            line-height: 25px;
            .span2 {
              color: #5e95f4;
              font-size: 18px;
              font-weight: 600;
              position: absolute;
              left: 77px;
              top: -1px;
            }
            .span3 {
              position: absolute;
              top: 1px;
              left: 88px;
            }
          }
        }
        .progress {
          .progress-item {
            display: flex;
            margin-top: 19px;
            .item1 {
              font-size: 14px;
              font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
              font-weight: 400;
              color: rgba(50, 50, 50, 1);
              margin-right: 9px;
              margin-top: -2px;
            }
            .item2 {
              width: 305px;
              /deep/.el-progress-bar {
                padding-right: 9px;
              }
              /deep/.el-progress-bar__inner {
                background: linear-gradient(to right, #bae1ff 0%, #aac9ff 100%);
              }
              .is-answer {
                /deep/.el-progress-bar__inner {
                  background: linear-gradient(
                    to right,
                    #add942 0%,
                    #6fbd25 100%
                  );
                }
              }
            }
            .item3 {
              margin-top: -2px;
              font-size: 12px;
              font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
              font-weight: 400;
              color: rgba(167, 167, 167, 1);
              .span1 {
                margin-right: 5px;
              }
            }
          }
        }
        .answer-detail {
          overflow-x: scroll;
          width: 100%;
          .detail-item {
            display: -webkit-box;
            margin-top: 12px;
            width: 100%;
            height: 26px;
            border-bottom: 1px dashed #efefef;
            .item1 {
              display: flex;
              width: 70px;
              flex-shrink: 0;
              .item-item1 {
                font-size: 14px;
                font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
                font-weight: 400;
                color: rgba(50, 50, 50, 1);
              }
              .item-item2 {
                font-size: 12px;
                font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
                font-weight: 400;
                color: rgba(167, 167, 167, 1);
                margin-top: 1px;
                margin-left: 5px;
              }
            }
            .item2 {
              font-size: 14px;
              font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
              font-weight: 400;
              color: rgba(50, 50, 50, 1);
              .item-item {
                margin-right: 6px;
                white-space: nowrap;
              }
            }
          }
        }
        ::-webkit-scrollbar {
          display: none; /* Chrome Safari */
        }
      }
      .finish-raisehand {
        width: 100px;
        height: 30px;
        background: rgba(94, 149, 244, 1);
        border-radius: 4px;
        font-size: 14px;
        font-family: AlibabaPuHuiTi-Regular, AlibabaPuHuiTi;
        font-weight: 400;
        color: rgba(255, 255, 255, 1);
        line-height: 30px;
        text-align: center;
        position: absolute;
        right: 30px;
        bottom: 20px;
        cursor: pointer;
      }
    }
  }
}
</style>



