<template>
  <div class="left-bar">
    <div class="head-portrait" :style="{backgroundImage:`url(${headPortraitImg})`}">
    </div>
    <div class="course" @click="changeType(0)">
      <img :src="courseSelectImg" v-if="type == 0" />
      <img :src="courseSelectImg1" v-if="type == 1" />
    </div>
    <div class="user" @click="changeType(1)">
      <img :src="classImg" v-if="type == 1" />
      <img :src="classImg1" v-if="type == 0" />
    </div>
  </div>
</template>

<script>
import courseSelectImg from "@/assets/image/home_btn_subject_press@2x.png"
import courseSelectImg1 from "@/assets/image/home_btn_subject_hover@2x.png"
import classImg from "@/assets/image/home_btn_class_press@2x.png"
import classImg1 from "@/assets/image/home_btn_class_hover@2x.png"
import Api from '@/api'

export default {
  name: "leftBar",
  data() {
    return {
      type: 0,
      courseSelectImg,
      courseSelectImg1,
      classImg,
      classImg1,
      headPortraitImg: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3078898191,2318315632&fm=26&gp=0.jpg',
    };
  },
  mounted() {
    this.getUserInfo()
  },
  methods: {
    getUserInfo() {
      Api.User.userInfo().then((val) => {
        this.headPortraitImg = val.avatar
      })
    },
    changeType(type) {
      if (this.type != type) {
        this.type = type
        this.$emit('changeType', type)
      }
    }
  }
};
</script>

<style scoped lang="less">
.left-bar {
  height: 100%;
  position: fixed;
  width:70px;
  background:linear-gradient(173deg,rgba(74,74,89,1) 0%,rgba(54,54,69,1) 100%);
  .head-portrait {
    width: 50px;
    height: 50px;
    margin-top: 65px;
    margin-left: 11px;
    border-radius: 50%;
    background-position: 50% 50%;
  }
  .course {
    text-align: center;
    cursor: pointer;
    img {
      width: 30px;
      height: 30px;
      margin-top: 70px;
    }
  }
  .user {
    text-align: center;
    cursor: pointer;
    img {
      width: 30px;
      height: 30px;
      margin-top: 44px;
    }
  }
}
</style>



