<template>
  <div class="home">
    <div class="main">
      <ScreenBar @refresh="refreshByType"/>
      <LeftBar @changeType="changeType"/>
      <Course v-if="type == 0" ref="course"/>
      <CourseModal @refresh="refresh"/>
      <Class v-if="type == 1" ref="class"/>
    </div>
  </div>
</template>

<script>
import LeftBar from './components/leftBar'
import Course from './components/course'
import CourseModal from './components/courseModal'
import Class from './components/class'
import ScreenBar from './components/screenBar'
export default {
  name: "Home",
  components: {
    LeftBar,
    Course,
    CourseModal,
    Class,
    ScreenBar
  },
  data() {
    return {
      loginType: 0,
      type: 0,
    };
  },
  mounted() {
  },
  methods: {
    changeType(type) {
      this.type = type
    },
    refresh(type) {
      this.$refs.course.getCourse(type)
    },
    refreshByType() {
      if (this.type == 0) {
        this.$refs.course.getCourse(undefined, 'refresh')
      } else {
        this.$refs.class.getClass()
      }
    }
  }
}
</script>

<style scoped lang="less">
  .home {
    height: 100%;
    background: white;
  }
  .main {
    height: 100%;
  }
</style>



