import Axios from 'axios';
// 备注：, 在参数内传递 {msgAlert: false}，可以在接口返回错误的时候不提示错误信息
export default {
  login(params) {
    return Axios.post('oauth/v2/token',params);
  },
  // 退出登录
  logout() {
    return Axios.delete('/api/user/v1/logout');
  },
 
  
};



